// Integration profile logos
@mixin logo($width, $height) {
	width: $width;
	height: $height;
	border: 0.5px solid $border-light-color;
	background: $app-white;
	border-radius: 4px;
	box-sizing: border-box;
	text-align: center;

	svg { height: $height; width: $width; }
	&.custom-integration, &.email {
		svg { zoom: 0.98; }
	}
}

@mixin loader($pos-top, $pos-left) {
	.ember-loader {
		position: absolute;
		fill: $brand-theme-color;
		width: 30px;
		height: 30px;
		top: $pos-top;
		left: $pos-left;
	}
}

// Integration creation/edit page (steps page)
&.alerts_admin_itom_alert_profile_integration.page-layout__page-wrapper {

	.page-sidebar.step-tabs {
		background: $app-white;
		counter-reset: tabs-counter;
		width: 320px;

		.tab-link {
			align-items: center;
			background: $background-color-2;
			border-radius: 4px;
			box-sizing: border-box;
			color: $primary-text-color-1;
			counter-increment: tabs-counter;
			display: flex;
			font-size: 14px;
			font-weight: $text-weight-medium;
			justify-content: flex-start;
			line-height: 1;
			margin: 16px auto;
			padding: 10px 16px;
			position: relative;
			width: 100%;

			&:first-child {
				margin-top: 0;
			}

			&:hover {
				color: $linkColor;

				&:before {
					background: $linkColor;
				}
			}

			&:before {
				align-items: center;
				background: $tab-icon-color;
				border-radius: 50%;
				color: $app-white;
				content: counter(tabs-counter);
				display: flex;
				font-size: $text-normal;
				font-weight: $text-weight-semi-bold;
				height: 20px;
				justify-content: center;
				margin-right: 12px;
				min-width: 20px;
			}

			&.active {
				background: $section-background-blue !important;
				color: $linkColor !important;

				&:before {
					background: $linkColor !important;
					color: $white !important;
				}

				&:after {
					border-bottom-left-radius: 4px;
					border-left: 4px solid $linkColor;
					border-top-left-radius: 4px;
					color: $linkColor;
					content: "";
					height: 100%;
					left: 0;
					top: 0;
					position: absolute;
				}
			}

			&.completed {
				color: $app-grey;

				&:after { border-color: $app-success-state; }

				&:before { background: $app-success-state !important;}

				&:hover {
					background: $section-background-blue;
					color: $linkColor;
				}

				&.active {
					background: $background-color-2 !important;
					color: $app-grey !important;
				}
			}

			&.disabled {
				background: $dropdown-hover;
				color: $tab-disabled-color;

				&:before {
					background: $border-dark-color;
					color: $tab-disabled-icon-color;
				}
			}
		}
	}

	.page-content.steps {

		.step-footer {
			align-items: center;
			display: flex;
			justify-content: flex-end;
			padding: 0 0 52px 0;

			button.step-cancel { margin-right: 12px; }

			button.step-next {
				margin-right: 0;

				svg {
					fill: $app-white;
					height: 20px;
					margin-right: -8px;
					vertical-align: middle;
					width: 20px;
				}

				&.disabled {

					svg { fill: $disabled-text-color; }
				}
			}
		}


		[class^='step-'] {
			.title {
				font-size: $text-large;
				font-weight: 500;
				margin-bottom: 0;
			}

			.sub-title {
				font-size: $text-normal;
			}

			// step 2,3
			.card-row {
				align-items: center;
				border-radius: 4px;
				box-sizing: border-box;
				display: flex;
				justify-content: flex-start;
				margin: 16px 0;
				min-height: 52px;
				padding: 8px 16px;

				&:not(.card-row-title) {
					background: $app-white;
					border: 1px solid $border-light-color;
					box-shadow: 0 1px 6px rgba(22, 29, 37, 0.05);

					&:hover {
						background: $background-color-2;
					}
				}

				&.card-row-title {
					font-size: $text-normal;
					font-weight: $text-weight-semi-bold;
					line-height: 1;
					margin-bottom: 24px;
					margin-top: 0;
					min-height: 40px;
				}
			}
		}

		@import 'integration-steps/step-1-configuration';
		@import 'integration-steps/step-2-field-mapping';
		@import 'integration-steps/step-3-severity-mapping';
		@import 'integration-steps/step-4-authorization';
	}
}
