// Alert Profiles list page
&.alerts_admin_itom_alert_profiles.page-layout__page-wrapper {
	.btn-feedback span{
		vertical-align: top;
		margin-left: 4px;
	}
	.page-content {
		padding: 16px 16px 0;

		.profiles-list {
			margin-top: 16px;

			.profile-list-item {
				&.admin-ams-card.with-count {
					height: 78px;

					.content {
						height: 46px;
						width: calc(100% - 156px);
					}

					.logo-section {
						align-items: flex-start;
						border: none;
						border-left: 1px solid $border-dark-color;
						border-radius: 0;
						color: $primary-text-color-1;
						display: flex;
						flex-direction: column;
						font-size: 12px;
						height: auto;
						padding-left: 12px;

						.count {
							font-size: $text-large;
							line-height: 1.25;
							padding-left: 4px;
						}

						.ellipsis { width: 72px; }
					}

					&:hover {
						.logo-section {
							background-color: transparent;
						}
					}
				}
			}

		}
	}
}

// Alert Profile details page
&.alerts_admin_itom_alert_profile.page-layout__page-wrapper {
	.page-title {
		div:last-child { margin-left: initial; }
	}

	.page-content {
		padding: 16px 0 0 0;

		.pull-right {
			margin-left: auto;

			.btn {
				height: 24px;
				margin-left: auto;
				margin-right: 8px;
				min-width: 24px;
				width: 24px;

				&:last-child {
					margin-left: 0;
					margin-right: 16px;
				}

				svg {
					fill: $secondary-text-color-1;
					height: 12px;
					width: 12px;
				}
			}
		}

		.pad-lr-16 {
			padding-left: 16px;
			padding-right: 16px;
		}

		.ams-alert-profile-detail-header {
			align-items: center;
			display: flex;
		}

		.empty-list {
			width: unset !important;
		}

		.ams-title {
			font-size: $text-large;
			text-align: justify;
			@extend .pad-lr-16;
			width: calc(100% - 108px);
			word-break: break-all;
		}

		.ams-sub-title {
			color: $secondary-text-color-1;
			font-size: $title-small;
			padding-top: 12px;
			text-align: justify;
			@extend .pad-lr-16;
		}

		.tabs {
			border-bottom: none;
			margin-bottom: 0;
			margin-top: 24px;

			.tabs__items {
				@extend .pad-lr-16;
				border-bottom: 1px solid $border-light-color-2;

				.tabs__item__nav {
					margin: 0 32px 0 0;
					padding: 0;
				}
			}

			.tabs__content {
				min-height: 480px;

				&__pane {
					padding: 16px;
				}

				// Tab 1
				#integrations.tabs__content__pane {
					padding-bottom: 0;

					.integrations-list {
						.add-integration-btn {
							align-items: center;
							color: $app-grey;
							display: flex;
							float: right;
							font-weight: $text-weight-semi-bold;
							outline: none;

							svg.add {
								height: 14px;
								padding-right: 4px;
								vertical-align: middle;
								width: 14px;
								margin-right: 4px;
								box-sizing: unset;
							}

							&.disabled {
								color: $disabled-text-color;

								svg.add {
									.add-outer {
										fill: $white;
										stroke: $disabled-text-color;
									}

									.add-sign {
										fill: $disabled-text-color;
									}
								}
							}
						}
					}

					.default-content {
						padding-top: 32px;

						.ams-sub-title { width: 50%; }
					}

					.integration-list {
						clear: both;
						min-height: 382px;
						padding-top: 16px;

						.integration-list-item {
							cursor: default;

							.content-link {
								cursor: pointer;
								width: calc(100% - 64px);

								.logo-section { float: left; }

								.content { width: auto; }
							}

							.toggle-switch {
								cursor: pointer;
								margin-bottom: auto;
								margin-right: 16px;
								margin-top: auto;

								&.disabled {
									.slider {
										background-color: $radioDisabledBorder;

										&:before { border-color: $radioDisabledBorder; }

										svg { fill: $radioDisabledBorder; }
									}
								}
							}
						}
					}
				}
			}
		}
	}
}
