&.alerts_admin_itom_alert_integrations_source_list{
	padding: 24px 0;
	background: $app-white;
	min-width: 1260px;
	min-height: calc(100vh - 60px);

	.title-wrapper{
		@include flexbox;
		width: 1186px;
		margin: 0 auto;

		.title {
			font-weight: $text-weight-semi-bold;
			@include font-size($title-extra-large) ;
			/* identical to box height */
			color: $primary-text-color-1;
		}

		.back-btn{
			margin-right: 44px;
			box-shadow: 0px 2px 10px $box-shadow-color-2;
			border: none;
			border-radius: 6px;
			background: $app-white;
		}
	}

	.content-wrapper{
		width: 1040px;
		margin: 0 auto;
	}

	.search-input-container{
		.search-item-input { width: 100%; }

		.search-input-wrapper {
			background: $app-white;
			border: 1px solid $border-light-color;
			border-radius: 8px;
			box-shadow: 0 4px 16px rgba(51, 51, 51, 0.08), 0 4px 4px rgba(51, 51, 51, 0.04);
			height: 50px;
			margin-top: 10px;
			padding: 16px;
			&.active {
				border-color: $inputFocusBorder;
				box-shadow: 0 0 0 1px $inputFocusBorder;
			}
		}
	}

	.integration-list-container{
		width: 100%;

		.view-more-toggle {
			margin-top: 16px;
			padding: 0;
		}

		.integration-list-title {
			margin-top: 24px;
			margin-bottom: 12px;
			@include font-size($title-small);
			color: $primary-text-color-1;

			.custom-integration-font {
				color: $app-primary;
				cursor: pointer;
			}
		}

		.searching-text{
			@include flexbox();
			@include justify-content(flex-start);
			@include align-items(center);
		}

		.cards-container {
			@include flex-hv-center();
			@include justify-content(flex-start);
			flex-wrap: wrap;
			gap: 24px;

			.mini-card {
				background: $app-white;
				border: 1px solid $border-light-color;
				border-radius: $radius;
				box-shadow: 1px 4px 7px rgba(207, 215, 223, 0.4);
				box-sizing: border-box;
				cursor: pointer;
				@include flexbox();
				@include justify-content(flex-start);
				@include align-items(center);
				width: 242px;
				height: 72px;
				padding: 12px;
				position: relative;
				text-align: center;
				margin: 0;

				&:hover {
					background: $background-color-2;
					border: 1px solid $border-light-blue;
					border-radius: $radius;
					box-shadow: 0 1px 6px rgba(22, 29, 37, 0.05);
					box-sizing: border-box;
				}

				.logo-section {
					background: $app-white;
					border: 0.5px solid $border-light-color;
					border-radius: $radius;
					box-sizing: border-box;
					width: 48px;
					height: 48px;
					min-width: 48px;
					margin-right: 12px;
					@include flex-hv-center();

					img { height: 32px; width: 32px; }

					&.sumo-logic {
						img { padding-top: 6px; }
					}

					&.new-relic-apm {
						img {
							height: 36px;
							padding-top: 4px;
							width: 36px;
						}
					}

					&.new-relic-synthetics {
						img {
							height: 40px;
							width: 40px;
						}
					}

					&.custom-integration, &.email {
						.logo-wrapper {
							background: $app-grey;
							border-radius: 50%;
							height: 24px;
							width: 24px;
							@include flex-hv-center();

							img { height: 14px; width: 14px; }
						}
					}

					&.microsoft-scom {
						img { height: 40px; width: 40px; }
					}

					&.logicmonitor {
						img { height: 32px; width: 32px; }
					}
				}

				.content {
					@include flexbox();
					@include align-items(flex-start);
					@include flex-direction(column);
					@include justify-content(space-evenly);
					height: 80px;
					text-align: left;

					.name {
						width: 160px;
						font-size: $baseFontSize;
						font-weight: $text-weight-medium;
					}
				}

				.btn-icon {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					font-weight: $text-weight-medium;
					background: $btnBackgroundHighlight;
					display: none;

					.back-side-text {
						margin-left: 12px;
					}

					svg {
						width: 20px;
						height: 20px;
					}
				}
			}

			.mini-card:hover .btn-icon {
				display: inline-flex;
			}
		}
	}
}

