.workspace-edit {
	.titlebar {
		margin: 0 !important;
		.workspace-title {
			font-weight: $text-weight-semi-bold;
			font-size: $title-normal;
		}
	}
	.sticky-element {
		background: $white !important;
		padding: 20px 24px !important;
		border-bottom: 1px solid $border-light-color;
	}
	.page-content {
		width: 100%;
		background: $white;
		margin: 0;
		.workspace-details {
			padding: 24px 32px 100px;
			overflow-y: scroll;
			.workspace-fields {
				.workspace-logo-label {
					font-weight: $text-weight-semi-bold;
					font-size: $text-normal;
				}
			}
			.workspace-logo {
				margin-bottom: 24px;
				.logo-wrapper {
					border: 1px dashed $disabled-text-color;
					border-radius: 8px;
					display: inline-flex;
					align-items: center;
					padding: 8px 10px;
					.logo-change-container {
						height: 34px;
						margin-left: 10px;
						border-left: 1px dashed $disabled-text-color;
						display: flex;
						align-items: center;
						label {
							margin-left: 12px;
							font-weight: $text-weight-medium;
							font-size: $text-normal;
							padding: 0;
							&:focus {
								box-shadow: 0 0 0 2px $linkColor;
							}
						}
					}
					#workspace-img {
						width: 42px;
						height: 42px;
						border-radius: 8px;
						flex-shrink: 0;
					}
					#logo-uploader {
						display: none;
					}
				}

			}
			.ember-form .fieldset-wrapper {
				width: 510px;
				margin-bottom: 16px;
			}
			.workspace-admins, .workspace-agents, .workspace-requesters {
				background: $background-color-2;
				border-radius: 8px;
				padding: 24px;
				width: 681px;
				margin-bottom: 12px;
				.ws-user-header {
					display: flex;
					.header-text {
						.ws-user-title {
							font-weight: $text-weight-semi-bold;
							font-size: $title-normal;
							margin: 0;
						}
						.ws-user-desc {
							font-size: $text-normal;
							margin: 4px 0 0;
						}
					}
					.header-cta {
						margin-left: auto;
						.view-roles {
							display: flex;
							align-items: center;
							background: $border-light-color;
							border-radius: 3px;
							padding: 6px 10px;
							svg {
								width: 12px;
								height: 16px;
								path {
									fill: $primary-text-color-1;
								}
							}
							p {
								font-weight: $text-weight-medium;
								font-size: $text-normal;
								margin: 0 0 0 9px;
							}
						}
					}
				}
				.ws-user-content {
					.add-user {
						display: flex;
						align-items: center;
						margin-top: 20px;
						.plus-icon-container {
							width: 14px;
							height: 14px;
							border-radius: 50%;
							display: flex;
							flex-shrink: 0;
							justify-content: center;
							align-items: center;
							background: $app-success-state1;
							svg {
								fill: $white;
								width: 7px;
								height: 7px;
							}
						}
						.add-user-text {
							color: $linkColor;
							margin: 0 0 0 5px;
						}
					}
				}   
			}
			.workspace-requesters {
				.requester-container {
					border: 1px solid $border-dark-color;
					box-shadow: inset 0px 1px 2px rgba($app-primary-black, 0.05);
					border-radius: 4px;
					padding: 4px;
					margin-top: 16px;
					.all-req-container {
						background: $background-color;
						border-radius: 4px;
						padding: 5px 8px;
						font-weight: $text-weight-semi-bold;
						font-size: $text-normal;
						color: $secondary-text-color-2;
						display: inline-block;
					}
				}
			}
			.workspace-delete {
				button {
					display: flex;
					align-items: center;
					padding: 16px 20px;
					width: 681px;
					height: auto;
					line-height: 20px;
					color: $app-danger-red !important;
					background: none;
					svg {
						width: 14px;
						height: 18px;
						flex-shrink: 0;
						fill: $app-danger-red;
					}
					.delete-ws-btn-text {
						margin: 0 0 0 7px;
					}
				}
			}
		}
		footer {
			position: fixed;
			bottom: 0;
			background: $white;
			box-shadow: 0px -4px 15px rgba($app-black, 0.06);
			padding: 24px;
			left: 60px;
			width: 100%;
			.btn-primary {
				margin: 0;
			}
			.button-containers {
				width: 690px;
				display: flex;
				justify-content: flex-end;
			}
		}
	}
}
.workspace-delete-confirm-modal, .workspace-publish-confirm-modal {
	width: 512px !important;
	.ember-modal-header .title {
		font-weight: $text-weight-semi-bold;
		line-height: 24px;
	}
	.ember-modal-footer {
		padding-right: 16px;
	}
}
.workspace-publish-confirm-modal {
	top: 242px !important;
	.publish-workspace-info {
		list-style-type: disc;
		margin-left: 12px;
		li{
			margin-top: 12px;
		}
	}
	.publish-workspace-info-title, .btn-primary {
		margin: 0;
	}
	.ember-modal-body {
		max-height: none !important;
	}
	.other-publish-settings {
		background: $section-background-blue;
		border-radius: 4px;
		padding: 12px 16px;
		margin-top: 24px;
		.other-settings-title {
			font-weight: $text-weight-semi-bold;
			font-size: $text-normal;
			color: $secondary-text-color-1;
		}
		ul {			
			li {
				display: flex;
				align-items: center;
				input {
					flex-shrink: 0;
					margin-right: 12px;
				}
				&:first-child {
					margin: 10px 0 12px;
				}
				label {
					padding: 0;
				}
			}
		}
	}
}
.workspace-delete-confirm-modal {
	.delete-workspace-info, .btn-danger {
		margin: 0;
	}
}
