
$light-blue-border: #BBDCFE;

// Common style for the entire module
.bo-label {
	display: block;
	margin-top: 10px;
	margin-bottom: 5px;
}

.id-field-info-icon {
	position: relative;
	top: 3px;

	svg {
		width: 20px;
		height: 16px;
		fill: $app-info-color;
	}
}

.bo-downgrade-container {
	width: auto;
	height: 24px;
	padding: 16px;
	background: $app-casablanca-light;
	font-weight: $text-weight-medium;
	.text {
		float: left;
		line-height: 16px;
	}
	.icon {
		float: left;
		margin-right: 8px;
		position: static;
		svg {
			width: 16px;
			height: 16px;
		}
	}
}

.module-business-objects {
	.delete-bo-btn {
		color: $app-red;
	}
	.bo-btn-group a{
		margin-right: 10px;
	}
	.bo-sidebar {
		padding: 20px 24px;
		.sidebar-title {
			margin-bottom: 16px;
		}
	}
	.bo-wrapper {
		background-color: $app-white;
		min-height: 650px;
		border-radius: 4px;
		.empty-bo-img {
			margin-bottom: 20px;
		}
		.create-bo {
			outline: transparent;
		}
	}
	.bo-container {
		.header	{
			background-color: $app-sidebar-bg;
			height: 52px;
			font-size: 16px;
			display: flex;
			align-items: center;
			padding-left: 16px;
			position: relative;
			z-index: 0;
		}
		.business-object-list {
			.bo-item {
				padding: 16px 20px;
				border-bottom: 1px solid $border-dark-color;
				word-break: break-word;
				.item-desc {
					margin-top: 6px;
					color: $inputHoverBorder;
				}
				&:hover {
					background-color: $app-sidebar-bg;
				}
			}
			.infinity-loader {
				padding: 20px;
				display: flex;
				justify-content: center;
				background: url('/a/assets/images/ajax-loader.gif') no-repeat;
				background-position: 50%;
				span {
					display: none;
				}
				&.reached-infinity {
					display: none;
				}
			}
		}
	}
}

// Business object Create Modal
.business-object-modal {
	.form-fields {
		.error {
			margin-top: 5px;
			margin-bottom: 10px;
		}
		.form-field {
			input[type="text"] {
				width: 98%;
			}
			&:not(:first-child) {
				margin-top: 15px;
			}
			.star {
				color: $app-red;
			}
			textarea {
				height: 100px;
			}
		}
		&.error {
			label {
				color: $inputColor;
			}
			input[type="text"] {
				border-color: $app-red !important;
			}
			input[type="text"]:focus {
				border-color: $inputFocusBorder !important;
			}
		}


	}
}


