// Step 3: Severity mapping
.step-3 {
	.title {
		padding-bottom: 8px;
	}

	&.content { padding-top: 24px; }

	.content {
		.card-row {

			label { padding-bottom: 0; }

			.left {
				@include set-severity-tags();
				min-width: 20%;

				[class^='state'] {
					text-transform: capitalize;
				}

				.state-ok {
					span {
						position: relative;
						text-transform: uppercase !important;
					}

					&.mandatory-field {
						span:after {
							color: $app-error-state;
							content: "*";
							padding-left: 2px;
							position: absolute;
							top: -2px;
						}
					}
				}
			}

			.right {
				flex: 1;
				max-width: 80%;
				padding: 2px 2px 0 2px;

				.fs-multiple-option {
					padding: 2px 8px;

					.fs-multiple-option-text {
						font-weight: normal;
					}
				}

				.fs-multiple-options-content {
					border: 2px solid transparent;

					&.focus {
						border: 2px solid $inputFocusBorder !important;
					}

					&:not(:focus) {
						box-shadow: none;
					}
				}

			}

			.error {
				color: $app-red-1;
				font-size: $text-normal;
				margin-top: 8px;
			}
		}
	}
}
