.created-workspaces, .explore-workspaces {
	.shimmer-loader {
		.card-title {
			display: none;
		}
	}
	.workspace {
		background: $white;
		border: 1px solid $border-dark-color;
		border-radius: 8px;
		width: 412px;
		display: inline-block;
		vertical-align: top;
		&:hover {
			box-shadow: 0px 2px 16px rgba($primary-text-color-1, 0.1);
		}
		.ws-header-body-container {
			padding: 12px 12px 24px;
			.ws-header {
				display: flex;
				.ws-logo-name {
					display: flex;
					align-items: center;
					.ws-logo {
						width: 42px;
						height: 42px;
						border-radius: 8px;
						flex-shrink: 0;
						margin-right: 4px;
					}
					.ws-name {
						font-weight: $text-weight-semi-bold;
						font-size: $title-normal;
						margin: 0;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}
				}
			}
			.ws-description {
				margin-top: 8px;
				color: $secondary-text-color-1;
			}
		}
	}
}

#explore-workspace-container {
	width: 472px;
	border: none;
	.explore-workspaces {
		height: 100%;
		background: $app-sidebar-bg-lighter;
		padding: 24px 32px 32px;
		.explore-ws-header {
			display: flex;
			p {
				font-weight: $text-weight-semi-bold;
				line-height: 17px;
				max-width: 290px;
			}
			svg {
				fill: $linkColor;
				margin-left: 8px;
				position: relative;
				top: 2px;
			}
			.header-btn {
				margin-left: auto;
				button {
					max-width: 130px;
					float: right;
					vertical-align: top;
					flex-shrink: 0;
					font-weight: $text-weight-semi-bold;
					text-align: right;
					padding-right: 0;
				}
			}
		}
		.explore-ws-list {
			margin-top: 24px;
			.workspace {
				margin-bottom: 16px;
			}
		}
		.shimmer-loader .card-content{
			margin-bottom: 16px;
		}
	}
}

.created-workspaces {
	width: 100% !important;
	background-color: $white;
	margin: 0 !important;
	padding: 24px 16px;
	border-radius: 0 !important;
	.created-workspace-list {
		display: flex;
		flex-wrap: wrap;
		li {
			display: flex;
			flex-direction: column;
			min-height: 210px;
			.ws-footer {
				margin-top: auto;
			}
		}
	}
	.shimmer-loader {
		> div {
			display: inline-flex;
		}
		.card-content  {
			width: 412px;
			margin: 0 16px 16px 0;
		}
	}
}
