section.authorization-content {
	padding: 0 16px;
	.title {
		font-weight: $text-weight-semi-bold;
	}
	.content {
		margin: 8px 0;
		.form-field {
			width: 300px;
			margin-bottom: 12px;
			label {
				color: $app-form-label-color;
				font-weight: $text-weight-semi-bold;
				@include font-size($text-normal);
				margin-bottom: 4px;
			}
		}
		.panel {
			label {
				color: $app-desaturated-black;
				margin-bottom: 8px;
				@include font-size(12px);
				font-weight: $text-weight-semi-bold;

				&.email-label {
					@include font-size(14px);
				}
			}
			.info {
				@include flexbox-simple(flex-start, center);
				margin-bottom: 8px;
				&.info-header {
					margin-bottom: 12px;
				}
				&.info-params {
					margin-bottom: 0px;
				}
				.info-text {
					background: $app-sidebar-bg;
					color: $btnDisabledColor;
					border: 1px solid $primary-border;
					border-radius: 4px;
					padding: 16px 100px 16px 12px;
					width: 100%;
					overflow-wrap: break-word;
				}
				.copy-btn {
					margin-left: -96px;
				}
			}
		}
		.support-link {
			font-weight: $text-weight-semi-bold;
			@include flexbox;
			@include align-items(center);
			@include font-size(12px);
			margin-top: 16px;
			&.hide {
				display: none;
			}
			svg {
				width: 12px;
				height: 12px;
				fill: $linkColor;
				margin-left: 4px;
			}
		}
	}
}
