.create-schedule-popup {
	.addDescription {
		color: $app-primary;
		text-decoration: none;
		cursor: pointer;
	}
	.schedule-popup-common {
		& > input {
			&:focus {
				border-color: $inputFocusBorder !important;
				box-shadow: 0 0 0 1px $inputFocusBorder !important;
				outline: 0;
				outline: thin dotted \9;
				/* IE6-9 */
			}
		}
		label {
			margin-bottom: 6px;
		}

		.agentGroupTooltip {
			padding: 8px;
			background: $app-grey;
		}

		.disableAgentgroupDropdown {
			background: $background-color;
			.ember-power-select-selected-item {
				color: $secondary-text-color-2;
			}
		}
		.disabledGroup {
			max-width: 300px !important;
		}
		.disableAgentgroup {
			color: $disabled-text-color;
		}
		.ember-power-select-trigger:hover {
			border: 1px solid $border-dark-color;
		}
	}

	.ember-modal-overlay .ember-modal-dialog {
		.ember-modal-header {
			.title {
				font-size: 18px;
				font-weight: 600;
			}
		}
	}
	.border-red {
		border-color: $app-red !important;
	}	
	#agentGroup {
		.ember-basic-dropdown  .ember-basic-dropdown-trigger[aria-disabled=true] {
			background: $background-color;
			.ember-power-select-selected-item {
				color: $secondary-text-color-2;
			}
		}
	}
}

