&.alerts_admin_itom_automation_settings.page-layout__page-wrapper{
	.page-layout-content {
		.page-content {
			padding: 0;
			.tabs .tabs__items {
				border-bottom: 2px solid $border-light-color;
			}
		}
		.page-sidebar {
			height: calc(100vh - 60px);
		}
	}
}
.alert-automation-wrapper {
	padding: 0;
	margin: 0;
	.alert-intelligence-content {
		padding: 16px;
		border: 1px solid $border-light-color;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
		.alert-intelligence-title {
			position: relative;
			font-weight: $text-weight-semi-bold;
			@include font-size(16px);
			//Todo: Will be removed in future after the automated grouping enabled to all customers
			&::after {
				content: attr(data-status);
				position: absolute;
				color: $white;
				background: $app-warning-state;
				@include font-size(12px);
				top: 4px;
				padding: 0 8px;
				margin-left: 4px;
				border-radius: 10px;
			}
		}
		.freddy-text {
			@include font-size(14px);
			margin-top: 4px;
		}
		.card-wrapper {
			@include flexbox-simple(flex-start, flex-start);
			padding: 32px;
			.card-container {
				@include flexbox-simple(center, flex-start);
				margin-right: 40px;
			}
			.non-eligible {
				.early-access {
					margin-bottom: 24px;
					@include font-size(18px);
					font-weight: $text-weight-semi-bold;
				}
				.list-requirement {
					text-align: left;
					margin: 24px 0;
					li {
						@include flexbox-simple(flex-start, center);
						&:first-child {
							margin-bottom: 16px;
						}
						svg {
							width: 14px;
							margin-right: 14px;
						}
					}
				}
				.notify {
					opacity: 0.5;
					width: 480px;
				}
			}
			.eligible {
				.message-title {
					font-weight: $text-weight-semi-bold;
					@include font-size(18px);
				}
				.message-content {
					max-width: 600px;
					margin: 16px auto 0;
				}
				button.alert-toggle {
					margin-top: 40px;
					text-align: center;
					display: block;
					&.btn-secondary:focus {
						border: 1px solid $btnBorder !important;
					}
				}
			}
		}
	}
	.grouping-item-title {
		@include font-size(16px);
		margin-top: 32px;
		h3 {
			font-weight: $text-weight-semi-bold;
		}
	}
	.automated-grouping {
		width: 100%;
		height: 88px;
		padding: 0 24px;
		@include flexbox-simple(flex-start, center);
		margin-top: 16px;
		background: $white;
		border: 1px solid $border-light-color;
		box-sizing: border-box;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
		border-radius: 4px;
		.left-container {
			width: 100%;
			.automated-grouping-title {
				font-weight: $text-weight-semi-bold;
				@include font-size(14px);
			}
			.automated-grouping-desc {
				margin: 4px 0 0 0;
				color: $secondary-text-color-1;
			}
			a {
				margin-top: 4px;
				display: inline-block;
				font-weight: $text-weight-semi-bold;
			}
		}
	}
}
//Todo: Will be removed in future after the automation settings enabled to all customers
.tabs__item[aria-controls="automated-grouping"] {
	.tab-title--tab-icon  {
		padding-right: 52px;
		&::after {
			position: absolute;
			content: 'New';
			right: 8px;
			top: 12px;
			color: $white;
			background: $app-warning-state;
			@include font-size(12px);
			padding: 0 10px;
			margin-left: 4px;
			border-radius: 10px;
		}
	}
}