.response-suggestions {
	.response-suggestion-sidebar {
		padding: 16px;
		width: 300px;

		.title{
			font-weight: $text-weight-semi-bold;
			font-size: $text-large;
			padding-bottom: 8px;
		}
		.help-text{
			margin-bottom: 8px;
			margin-right: 20px;
		}
	}

	.response-suggestions-body {
		padding: 16px 24px;
		background-color: $app-white;
		border-radius: $inputBorderRadius;
		height: calc(100% - 32px);
	}

	.response-suggestions-body--header {
		font-weight: $text-weight-semi-bold;
		display: inline-block;
		font-size: $title-large;
		padding: 8px 4px 16px;
	}

	.response-suggestions-body--info {
		background: $background-color-2;
		border-radius: 8px;
		display: flex;
		margin: 10px 0 24px;
		align-items: center;
		justify-content: center;
		color: $btnPrimaryActiveBackground;

		.freddy-image{
			img {
				padding-top: 10px;
				height: 65px;
				width: 100px;
			}
		}
		.message{
			padding: 8px 16px;
			border-left: 1px solid $app-border-primary;
		}
	}

	.response-suggestion-item-wrapper {
		border: 1px solid $border-light-color;
		border-radius: $inputBorderRadius;
		background-color: $app-white;
		margin-bottom: 8px;
		position: relative;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
	}

	.response-suggestion-item {
		padding: 24px;
	}

	.response-suggestion-item--content {
		width: calc(100% - 50px);
		display: inline-block;
	}
	
	.response-suggestion-item--content-label {
		display: inline-block;
		padding-bottom: 4px;
		font-weight: $text-weight-semi-bold;
		font-size: $title-normal;
	}
	
	.response-suggestion-item--content-description {
		color: $secondary-text-color-1;
		margin-top: 4px;
	}

	.response-suggestion-item--switch {
		width: 35px;
		display: inline-block;
		vertical-align: top;
	}
}