.email_configs_dkim_configs  {
	.page_layout__title-bar {
		display: none;
	}
	.page-layout-content {
		.page-content {
			background: $app-white;
			margin: 0;
			min-height: 100vh !important;
		}
		.dkim-section {
			margin: 24px 32px;
			.dkim-header {
				color: $secondary-text-color-1;
				.title {
					font-size: $title-normal;
					font-weight: $text-weight-semi-bold;
					color: $app-grey;
				}
				.helptext {
					width: 75%;
					line-height: $btnBaseLineHeight;
				}
				ul {
					margin-left: 8px;
					padding-left: 5px;
					li {
						margin-bottom: 10px;
						list-style: disc;
						&::marker {
							color: $app-green;
						}
						b {
							font-weight: $text-weight-semi-bold;
						}
					}
				}
			}
			.dkim-body {
				margin-top: 32px;
				.shimmer-loader {
					width: 75%;
					.shimmer-loader-item {
						margin-left: 0 !important;
					}
				}
				.ember-alert-info {
					width: fit-content;
					svg {
						fill: $app-grey;
					}
					box-shadow: none;
				}
				.ui-accordion-list {
					.ui-accordion-item-expanded .ui-accordion-header-trigger {
						border-bottom-left-radius: 0 !important;
						border-bottom-right-radius: 0 !important;
						border-bottom: 0 !important;
					}
					.ui-accordion-panel-wrapper {
						border: 1px solid $border-light-color;
						border-top: 0;
						.ui-accordion-panel-content {
							.shimmer-loader {
								margin-left: 8px;
							}
							.ember-alert {
								margin: 0px 15px 15px;
								padding: 8px;
								height: auto;
								.alert-content b {
									font-weight: $text-weight-semi-bold;
								}
								.alert-circle {
									margin-top: -16px;
								}
							}
							.dkim-footer {
								@include flexbox;
								@include justify-content(space-between);
								background: $background-color-2;
								padding: 10px;
								.setup {
									@include flexbox;
									@include align-items(center);
									margin-left: 5px;
									a {
										margin-left: 5px;
										font-weight: $text-weight-semi-bold;
									}
								}
							}
						}
					}
					.get-started .domain {
						margin-left: 10px !important;
					}
					.ui-accordion-item,
					.get-started {
						margin-top: 15px;
					}
					.ui-accordion-item .ui-accordion-header-trigger,
					.get-started {
						@include flexbox-simple(space-between, center);
						background: $app-white;
						border: 1px solid $background-color;
						padding: 8px;
						border-radius: 4px;
						height: 48px;
						box-shadow: 0px 1px 6px 0px #161d250d;
						.domain {
							font-weight: $text-weight-semi-bold;
							margin-left: 4px;
							@include flexbox;
							@include align-items(center);
							svg {
								margin-top: 1px;
								fill: $app-grey;
								margin-right: 5px;
							}
							span {
								color: $primary-text-color-1;
								font-weight: $text-weight-semi-bold;
							}
						}
						.status {
							padding: 4px 8px;
							border-radius: 4px;
							margin-right: 8px;
							font-size: $text-normal;
							border: 1px solid;
							&.show {
								opacity: 1;
								transform: translateX(0);
							}
							&.hide {
								opacity: 0;
								transform: translateX(100%);
								transition: transform ease-out .25s;
							}
							/* Not verified state */
							&-1 {
								background: $alertWarningBackground;
								color: $btnDangerBorder !important;
								border-color: $alertWarningBorder;
								font-weight: $text-weight-semi-bold;
							}
							/* Verified state */
							&-2 {
								background: $alertSuccessBackground;
								color: $alertSuccessColor !important;
								border-color: $alertSuccessBorder;
								font-weight: $text-weight-semi-bold;
							}
							&-1, 
							&-2 {
								height: 24px;
								@include flex-center;
							}
						}
					}
					.get-started .btn-primary {
						min-width: 85px;
						height: 24px;
						font-size: $text-normal;
						padding: 0 5px;
					}
					.verify-remove {
						.remove {
							margin-right: 10px;
						}
						.btn-primary {
							min-width: 85px;
							margin-right: 5px;
							&.disabled {
								color: $app-white !important;
								font-weight: $text-weight-semi-bold;
								background: $disabled-text-color !important;
							}
						}
					}
					.get-started .btn-primary svg,
					.verify-remove .btn-primary svg {
						fill: $white;
						animation: spin 1s linear infinite;		
					}
					.verify-remove .btn-primary svg {
						margin-top: -4px;
						width: 30px;
						height: 30px;
					}
					.dkim-record-table {
						width: 100%;
						tr {
							border-bottom: 1px solid $border-light-color;
						}
						thead {
							background: $background-color-2;
							height: 30px;
							th {
								font-size: $text-normal;
								font-weight: $text-weight-semi-bold;
							}
						}
						thead, tbody {
							th, td {
								&:first-child {
									width: 8%;
									padding-left: 15px;
									padding-right: 8px;
								}
								&:nth-child(2),
								&:nth-child(3) {
									width: 42%;
								}
								&:nth-child(2) svg {
									display: inline-block;
									vertical-align: text-top;
									fill: $primary-text-color-1;
								}
								&:last-child {
									width: 5%;
									text-align: center;
									display: table-cell;
									max-width: 96px;
									@extend .ellipsis;
									padding-right: 10px;
								}
							}
							td {
								padding-top: 15px;
								padding-bottom: 15px;
								&:first-child {
									padding-top: 20px;
									padding-bottom: 20px;
									font-size: $title-small;
									font-weight: $text-weight-medium;
								}
								&:nth-child(2) div,
								&:nth-child(3) div {
									@include flexbox;
									.host, .value {
										margin-right: 8px;
										border: 1px solid $border-light-color;
										padding: 5px;
										background: $inputDisabledBackground;
										width: 90%;
										border-radius: 4px;
										color: $disabled-text-color;
										span {
											margin-left: 8px;
											max-width: 392px;
											@extend .ellipsis;
										}
									}
								}
								&:last-child {
									padding-top: 20px;
									svg {
										vertical-align: bottom;
										&.record-success {
											fill: $app-success-state;
										}
										&.record-alert {
											fill: $app-error-state;
										}
									}
									.ember-tooltip {
										margin-left: 50px !important;
									}
								}
							}
						}
					}
				}
			}
		}
		.page-sidebar {
			min-width: 360px !important;
			.dkim-sidebar {
				margin: 24px 16px;
				svg {
					fill: $primary-text-color-1;
				}
				.spf-dkim {
					margin-top: 16px;
				}
				.dmarc {
					margin-bottom: 24px;
				}
				p {
					line-height: $btnBaseLineHeight;
				}
				ul {
					list-style-type: disc;
					list-style-position: inside;
					li {
						margin-bottom: 8px;
						line-height: $btnBaseLineHeight;
						b {
							font-weight: $text-weight-semi-bold;
						}
					}
				}
			}
		}
	}
}

.dkim-remove-modal .ember-modal-footer .remove svg {
	width: 30px;
	height: 30px;
	min-width: 56px;
	fill: $app-white;
	margin-top: -4px;
	animation: spin 1s linear infinite;
}
