// Step 2 : Alert Field Mapping
.step-2 {

	.sub-title {
		span:first-child { width: 75%; display: inline-block; }
	}

	&.content { padding-top: 24px; }

	.card-row {

		&:hover {
			button { display: block; }
		}

		.left-col {
			display: inline-block;
			max-width: 300px;
			min-width: 200px;

			.required { color: $app-red-1; }

			svg {
				cursor: pointer;
				height: 12px;
				outline: none;
				vertical-align: middle;
				width: 12px;

				path { fill: $linkColor; }
			}

			.info-tooltip-container {
				font-size: $text-normal;
				text-transform: initial;
			}
		}

		.center-col {
			display: inline-block;
			margin-left: 16px;
			margin-right: 16px;
			max-width: 400px;
			min-width: 320px;
			position: relative;

			.fs-multiple-options-content {
				border-radius: 2px;
				min-height: 32px;
				padding-right: 35px;
				width: 290px;

				&.no-data {
					padding-left: 4px;

					padding-top: 6px;

					&:before { content: '--'; }
					&.has-error {
						border: 1px solid $app-red-1 !important
					}
				}
				.fs-multiple-options {
					overflow: visible;
					max-height: unset;
					.fs-multiple-option {
						background: $background-color !important;
						border-radius: 4px;
						word-break: break-word;
						margin: 2px;

						.fs-multiple-option-text {
							font-weight: normal;
						}
					}
				}
			}

			.field-error {
				color: $app-red-1;
				font-size: $text-normal;
				padding-top: 8px;
			}

			&:hover {
				button {
					opacity: 1 !important;
				}
			}

			button.add-btn {
				background: $section-background-blue;
				border: 1px solid $primary-border;
				border-radius: 0 2px 2px 0;
				color: $linkColor;
				cursor: pointer;
				font-size: $title-small;
				height: 32px;
				line-height: 1;
				opacity: 0;
				outline: none;
				position: absolute;
				right: 30px;
				top: 0;
				padding: 0;

				svg {
					height: 30px;
					width: 30px;
				}

				&.disabled {
					background: $background-color !important;
					color: $disabled-text-color !important;
					cursor: not-allowed !important;
					pointer-events: auto !important;

					svg {
						path {
							&:first-child {
								fill: $btnDisabledBackground;
								stroke: $btnDisabledBackground;
							}
							&:last-child {
								fill: $disabled-text-color;
							}
						}
					}

					&:hover {
						color: $disabled-text-color;
						opacity: 0.5;
					}

					.add-code-light {
						&-sign {
							fill: $disabled-text-color;
						}
					}
				}

				&#add-field-tags, &#add-field-additional_info {
					height: 100%;
				}
			}

			// Field labels selection modal
			.ember-modal-dialog {
				&.ember-modal-dialog-in-place.field-labels-selector {
					background-color: #fff;
					border-radius: 4px;
					box-shadow: 0 1px 5px 0 #737373;
					left: 100%;
					margin-left: 16px;
					position: absolute;
					right: 0;
					top: 0;
					width: 376px;

					.ember-modal-header {
						padding: 16px;

						.title {
							font-size: $title-normal;
							font-weight: $text-weight-semi-bold;
							line-height: $baseLineHeight;
							margin-bottom: 0;
							text-align: left;
						}

						.ember-modal-close {
							cursor: pointer;
							float: right;
							padding-top: 4px;
							background: transparent !important;
							border: 1px solid transparent;
							svg {
								fill: $app-muted-color;
								height: 14px;
								width: 14px;
							}
						}
					}

					.ember-modal-body {
						padding: 0 16px 16px 16px;

						.subtitle {
							font-size: $text-normal;
							line-height: $baseLineHeight;
							text-align: left;
						}

						.labels-wrapper {
							margin-left: -8px;
							margin-right: -8px;
							margin-top: 8px;
							max-height: 160px;
							overflow: auto;
							padding: 8px 8px 0 8px;

							.payload-label {
								background: $app-white;
								border: 1px solid $primary-border;
								border-radius: 2px;
								box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
								box-sizing: border-box;
								color: $primary-text-color-1;
								cursor: pointer;
								display: block;
								font-weight: inherit;
								min-height: 32px;
								margin: 0 0 8px 0;
								padding: 6px 16px;
								text-align: left;
								word-break: break-word;

								&:hover {
									background: $background-color-2;
								}
							}
						}
					}
				}
			}

			.payload-label-selector {
				&:not(.active) {
					.ember-modal-dialog.field-labels-selector {
						display: none;
					}
				}

				.add-code-dark {
					display: none;
				}

				&.active {
					.add-btn {
						background: $linkColor;
						color: $app-white;
						display: block !important;
						opacity: 1;
					}

					.add-code-dark {
						display: initial;
					}

					.add-code-light {
						display: none;
					}
				}

				.add-field-tooltip {
					font-size: $text-normal;
					line-height: 1.5;
					width: 160px;

					.tooltip-arrow {
						display: none;
					}
				}
			}

		}

		.right-col {
			display: inline-block;
			max-width: 360px;
			min-width: 200px;
			word-break: break-word;
		}

		&[data-field-key='additional_info'] {
			.right-col {
				margin-top: -22px;
				white-space: pre-line;
			}
		}
	}

	& + .step-footer {
		padding-bottom: 180px; // extra bottom padding to accommodate last row modal
	}
}
