.cm-banner-wrapper {
	@include flexbox;
	width: 100%;
	.cm-banner-cont {
		width: 70%;
		.cm-banner-title {
			@include font-size($title-extra-large);
			font-weight: $text-weight-bold;
			padding-bottom: 8px;
		}
		.cm-banner-desc {
			@include font-size($title-small);
			font-weight: $text-weight-normal;
		}
	}
	img {
		margin: 0 30px;
		width: 300px;
		height: 230px;
	}
	.page-nav-wrap {
		margin-left: auto;
		flex-shrink: 0;
	}
}

.cm-banner-footer-dots {
	@include flexbox;
	@include justify-content(center);
	.footer-dot {
		width: 8px;
		height: 8px;
		display: inline-block;
		background: $app-border-primary;
		border-radius: 50%;
		margin-right: 16px;
		cursor: pointer;
		&.active {
			background: $app-icon-color;
		}
	}
}
