$shiftColors: #FEA47F,#25CCF7,#EAB543,#55E6C1,#4834d4,#fa8231,#1B9CFC,#F8EFBA,#58B19F,#D6A2E8,#B33771,#3B3B98,#FD7272,#6ab04c,#82589F,#6D214F,#182C61,#FC427B,#BDC581,#786fa6,#006266,#c7ecee,#f78fb3,#2ecc71,#EA2027;

.schedule-shifts {
	text-align: center;
	padding: 29px;
	background: $white;
	box-sizing: border-box;
	border-radius: 8px;
	.padding_bottom_30 {
		padding-bottom: 30px;
	}
	.associate-ticket {
		display: inline-flex;
		align-items: center;
	}
}

.shift-section {
	background: $app-white;
	border: 1px solid $border-light-color;
	box-sizing: border-box;
	box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
	border-radius: 8px;
	margin: 16px 0;
	.shift-section-title {
		background: $background-color-2;
		border-radius: 4px;
		margin: 16px;
		padding: 14px;
		.pull-right svg {
			fill: $primary-text-color-1;
			height: 17px;
			width: 25px;
		}
		.shift-display-title {
			width: 95%;
			font-weight: $text-weight-medium;
			font-size: $title-normal;
			line-height: 20px;
			display: inline-flex;
			word-break: break-all;
			.shift-display-timezone{
				border-left: 1px solid $border-dark-color;
				color: $app-form-label-color;
				padding-left: 12px;
				margin-left: 12px;
			}
		}

	}
	
	.phone-notification-banner {
		@include flexbox;
		@include align-items(center);
		margin: 16px;
		
		.notification-text {
			width: 100%;
			
			.update_contact_link{
				cursor: pointer;
				font-weight: $text-weight-semi-bold;
				margin: 0px 4px;
			}
		}
	}
}
.on-call-shift-container {
	.shift-container {
		padding-left: 16px;
	}
	.form-fields .form-field.shift-date {
		display: inline-block;
		margin-right: 16px;
		margin-top: 0;
	}
	.shift-breadcrumb-title {
		width: 65%;
		word-break: break-all;
	}
	.date-field {
		width: 315px;
		display: inline-flex;
		margin-right: 4px;
		.date-time-split-field {
			.date-control {
				width: 165px;
				float: left;
				.ember-basic-dropdown-content {
					width: 242px; 
				}
				.ember-power-calendar-nav-buttons button {
					background-color: $white;
					&:hover {
						background-color: $btnDisabledBackground;
					}
				}
			}
			.date-control+div{
				display: flex;
				.time-control {
					width: 143px;
					padding-left: 8px;
					.ember-power-select-dropdown {
						width: inherit;
					}
				}
			}
		}
		.shift-date {
			input {
				background-color: $app-white !important;
			}
		}
	}
	.shift-start-end-date {
		display: flex;
	}
	.start-date{
		display: inline-block;
		&.disable-date {
			.date-field-wrapper .ember-basic-dropdown-trigger {
				opacity: 0.4;
			}
			.date-control {
				pointer-events: none;
				input {
					background-color: $btnDisabledBackground !important ;
					border-radius: 4px;
					border: 1px solid $primary-border;
					color: $secondary-text-color-2;
				}
				svg {
					fill: $secondary-text-color-2;
				}
			}
		}
	}
	.shift-form-wrapper {
		padding-left: 32px;
		
		.phone-notification-banner {
			@include flexbox;
			@include align-items(center);
			width: 56%;
			
			.notification-text {
				width: 100%;
				
				.update_contact_link{
					cursor: pointer;
					font-weight: $text-weight-semi-bold;
					margin: 0px 4px;
				}
			}
		}
	}
	.shift-section-title {
		padding: 24px 0 12px;
		font-weight: $text-weight-semi-bold;
		font-size: $title-normal;
	}
	.add-end-date{
		position: relative;
		display: inline;
		width: 500px;
		.add-end-date-title {
			position: absolute;
			display: inline;
			width: inherit;
			top: 30px;
			left: 0;
			color: $app-primary;
			font-weight: $text-weight-semi-bold;
			cursor: pointer;
		}
	}
	.shift-top-common {
		width: 630px;
	}
	.roster-configuration {
		.agent-container {
			border-bottom: 0;
			.tabs__items {
				padding: 0;
			}
			.tabs__content__pane {
				padding: 20px 20px 0 0;
				.tab-contents {
					.agents-list-section {
						display: flex;
						align-items: center;
						position: relative;
						&.member-error {
							margin-bottom: 32px;
							.ember-basic-dropdown .ember-power-select-multiple-trigger {
								border: 2px solid $app-error-state;
								box-shadow: inset 0px 1px 2px rgba(24, 50, 71, 0.05);
							}
						}
						.ember-basic-dropdown {
							width: 320px;
							.margin-top-32 {
								margin-top: 32px;
							}
							.margin-top-0 {
								margin-top: 0;
							}
							li[aria-disabled="true"] {
								cursor: not-allowed;
							}
							.ember-power-select-multiple-options {
								flex-grow: 1;
							}
							input {
								padding: 0;
								&::placeholder {
									padding-left: 10px;
								}
							}
							.ember-power-select-status-icon {
								display: none;
							}
						}
						button {
							margin-left: 8px;
						}
						.agent-error, .member-error {
							display: none;
							color: $app-red;
							position: absolute;
							font-size: $title-small;
							top: 44px;
							&.show {
								display: block;
							}
						}
						.safari-only {
							margin-top: 32px;
						}
					}
					.helper-text {
						color: $secondary-text-color-2;
						margin: 8px 0 12px;
					}
					.agents-list  {
						.agent-content {
							display: flex;
							align-items: center;
							width: 320px;
							height: 40px;
							background: $background-color-2;
							border-radius: 4px;
							border-left: 5px solid;
							margin: 4px 0;
							.remove-agent {
								margin: 0 4px 0 8px;
								padding-top: 6px;
								background: none;
								cursor: pointer;
								border: none;
								svg {
									width: 14px
								}
							}
							span {
								margin-left: auto;
								margin-right: 8px;
								padding-top: 4px;
								svg {
									width: 16px;
								}
							}
							.agent-name {
								color: $primary-text-color-1;
								margin-left: 8px;
								white-space: nowrap;
								overflow: hidden;
								text-overflow: ellipsis;
								max-width: 195px;
							}
							
							.agent-invalid-ph {
								@include flexbox;
								margin: 0 4px 0 8px;
								background: none;
								cursor: pointer;
								border: none;
								vertical-align: middle;
								svg {
									width: 16px;
									path {
										fill: $app-error-state;
									}
								}
							}
							
							.agent-tooltip-container {
								span {
									display: none;
								}
							}
						}
						.dragSortItem.-placeholderAfter,
						.dragSortItem.-placeholderBefore {
							&:before {
								background: $dropdown-hover;
								border: 1px dashed $border-dark-color;
								box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
								border-radius: 4px;
								width: 320px;
								height: 40px;
								content: '';
							}
						}
						.dragSortItem.-placeholderAfter {
							.agent-content {
								margin-bottom: 20px;
							}
						}
						.dragSortItem.-placeholderBefore {
							.agent-content {
								margin-top: 20px;
							}
						}
						.dragSortList {
							margin-bottom: 8px;
						}
					}
					.rotation-type {
						.rotation-label {
							margin-bottom: 8px;
							font-size: $text-normal;
							font-weight: $text-weight-semi-bold;
							color: $primary-text-color-1;
						}
						.ember-basic-dropdown {
							width: 320px;
						}
					}
					.hand-off{				
						line-height: 20px;
						color: $secondary-text-color-2;
						padding: 12px 0 7px;
					}
					.rotation-details {
						.rotation-weekly {
							padding: 0 0 0 20px;
							border-left: 1px solid $border-dark-color;
							margin-top: 8px;
							margin-left: 2px;
							width: 320px;
							.rotation-weekly-label {
								margin-bottom: 8px;
								font-size: $text-normal;
								font-weight: $text-weight-semi-bold;
								color: $primary-text-color-1;
							}
						}
						.rotation-custom {
							padding: 0 0 0 20px;
							border-left: 1px solid $border-dark-color;
							margin-top: 20px;
							.rotation-every-label {
								margin-bottom: 8px;
								font-size: $text-normal;
								font-weight: $text-weight-semi-bold;
								color: $primary-text-color-1;
							}
							.rotation-custom-value {
								display: flex;
								align-items: center;
								input {
									width: 50px;
								}
								.ember-basic-dropdown {
									margin-left: 4px;
									width: 108px;
								}
								.border-red {
									border-color: $app-red !important;
								}
							}
							.show-custom-error {
								color: $app-red !important;
								padding-top: 5px;
								font-size: 12px;
								font-weight: 500;
							}
						}
					}
					.customize-shift{
						padding-top: 8px;
						.shift-toggle-container{
							padding: 8px 0;
							display: flex;
							font-weight: $text-weight-medium;
							>label{
								padding-left: 8px;
							}
						}
						.custom-shift-details{
							padding: 16px;
							border: 1px solid $dropdown-hover;
							background: $background-color-2;
							border-radius: 8px;
							width: 700px;
							display: block;
							color: $app-form-label-color;
							font-weight: $text-weight-semi-bold;
							.custom-shift-radio-group{
								display: flex;
								.radio-group .radio-field.form-field{
									width: 315px;
									label {
										font-weight: $text-weight-normal;
									}
								}
							}
						}
						.shift-times--section{
							padding: 10px 0 0 22px;
							position: relative;
							.time-picker-icon {
								display: none !important;
							}
							&.week {
								.shift-times .shift-time--delete{
									top: 2px;
								}
							}
							.shift-red-border {
								.time-control .ember-power-select-trigger {
									border: 1px solid $app-red;
									&:focus {
										border: 1px solid $app-red;
									}
								}
							}
							.customShiftError{
								padding: 9px 0 9px 35px;
								color: $app-red;
								font-size: 12px;
								font-weight: 500;
							}
							.customDay{
								padding-left: 4px;
							}
							.shift-times{
								padding: 4px 0;
								.shift-time{
									display: inline-block;
									padding: 0 2px;
									.shift-day--select{
										display: inline-block;
										min-width: 150px;
									}
									.shift-time--select{
										display: inline-block;
										width: 135px;
										margin-left: -2px;
									}
								}
								.shift-time--delete{
									.btn {
										margin-right: 0;
									}
									.delete-image{
										width: 16px;
										height: 16px;
									}
								}
							}
							.add-new-shift-time{
								padding-top: 4px;
								&.shift-day {
									position: absolute;
									bottom: 10px;
									left: 280px;
								}
								&.shift-day-red-border {
									position: absolute;
									bottom: 58px;
									left: 265px;
								}
								button {
									display: flex;
									align-items: center;
								}
								.add-image{
									width: 16px;
									height: 16px;
									margin-right: 8px;
								}
								.add-shift-time {
									font-size: $baseFontSize;
									padding-left: 5px;
								}
							}
						}
					}
				}
			}
		}
	}
	.btn-transparent{
		background: transparent !important;
		border: 1px solid transparent;
		padding: 4px !important;
		box-shadow: none;
		&:hover {
			border-color: transparent !important;
		}
	}
	.page-content{
		margin-top: 0;
	}
	/** Escalation path CSS starts here*/
	.sub-heading {
		@include font-size($title-normal);
		font-weight: $text-weight-semi-bold;
		margin-bottom: 12px;
	}

	.desc-text {
		max-width: 1320px;
		color: $secondary-text-color-2;
		margin-bottom: 18px;
		@include font-size($title-small);
	}

	.desc-title {
		@include font-size($title-small);
		font-weight: $text-weight-semi-bold;
		margin-bottom: 8px;
	}

	.inline-ui-comp {
		margin: 0px 4px;
	}

	input[type="number"].no-spin {
		-moz-appearance: textfield;
		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}
	}

	.alert-container {
		@include flexbox-simple(flex-start, center);
		background: $alertWarningBackground;
		border: 1px solid $alertWarningBorder;
		box-sizing: border-box;
		border-radius: 4px;
		padding: 4px 4px;
		width: fit-content;
		margin: 24px 0 0 16px;

		.alert-icon {
			border-right: 1px solid $alertWarningBorder;
			padding: 4px 8px;
			cursor: pointer;

			svg {
				fill: $btnDangerBorder;
				width: 16px;
				height: 16px;
			}
		}

		.alert-content {
			@include font-size($title-small);
			color: $app-grey;
			padding: 0px 12px;
			min-width: 552px;
		}

		.alert-close {
			padding: 0px 12px;
			font-size: $text-large;
			line-height: 24px;
			font-weight: $text-weight-medium;
			color: $app-primary-black;
			border: none;
			background: none;

			:not(:hover) {
				opacity: .5;
			}
		}
	}
	
	.agent-tooltip-container {
		padding: 8px 10px;
	}

	.escalation-path {
		margin: 32px 0px 0 32px;

		.escalation-path-rule-container {
			margin-bottom: 12px;

			.rule-row {
				border: none;
				background: none;
				padding-left: 0px;
				padding-bottom: 4px;
				&.error-row {
					margin-bottom: 24px;
				}

				.btn-add-condition {
					margin-left: 0px;
				}

				.btn-delete-condition {
					margin: 10px auto auto auto;
					padding: 2px;
					svg {
						height: 18px;
					}
				}

				.escalation-path-rule {
					padding: 8px 8px 0 8px;
					@include flexbox-simple(flex-start, flex-start);
					width: 100%;
					.fs-multiple-options-content {
						min-width: 480px;
						.fs-multiple-options {
							overflow: visible;
						}
					}

					.notifier-error {

						.fs-multiple-options-content {
							border: 2px solid $app-error-state;
							box-shadow: inset 0px 1px 2px rgba(24, 50, 71, 0.05);
						}
					}

					.ep-level-text {
						margin: 8px 8px 0px 4px;
						min-width: 46px;
					}

					.ember-basic-dropdown {
						margin-left: 4px;
					}

					.ui-dropdown-grouped .ember-basic-dropdown-content {
						width: 400px;

						.group-options .ember-power-select-options {
							max-height: 264px;
						}
					}
				}
			}

			.rule-note {
				margin: 4px 0 8px 92px;
			}
		}

		.ep-repeat-block { 
			@include flexbox-simple(flex-start, center);
			.ember-basic-dropdown.inline-ui-comp {
				width: 48px;
				.ember-power-select-trigger{
					min-width: 40px;
					.ember-power-select-selected-item {
						margin: auto;
					}
					.ember-power-select-status-icon {
						display: none;
					}
				}
			}
		}
	}
	.limit-error {
		font-weight: $text-weight-semi-bold;
		color: $app-error-state;
		@include font-size($title-small);
		margin-bottom: 8px;
		padding-left: 8px;
	}
	.error-text {
		margin-top: 4px;
		@include font-size($title-small);
		color: $app-error-state;
		position: absolute;
		b {
			font-weight: $text-weight-semi-bold;
		}
	}
	.stakeholder-notification {
		margin: 32px 0 40px 32px;
		&.dd-open {
			height: 750px;
		}
		.action-container {
			@include flexbox-simple(flex-start, flex-start);

			.fs-multiple-options-content .fs-multiple-options {
				overflow: visible;
			}

			.interval-error {
				.inline-ui-comp {
					border: 2px solid $app-error-state;
					box-shadow: inset 0px 1px 2px rgba(24, 50, 71, 0.05);
				}
			}

			.notify-error {
				.fs-multiple-options-content {
					border: 2px solid $app-error-state;
					box-shadow: inset 0px 1px 2px rgba(24, 50, 71, 0.05);
				}
			}

			.channel-error {
				.ember-basic-dropdown .ember-power-select-trigger {
					border: 2px solid $app-error-state;
					box-shadow: inset 0px 1px 2px rgba(24, 50, 71, 0.05);
				}
			}

			.action-text {
				margin-top: 8px;
				white-space: nowrap;
			}

			.inline-ui-comp {
				width: 40px;
			}

			.sh-notfiers-container {
				margin: 0px 4px;
				.fs-multiple-options-content {
					max-width: 440px;
				}
			}

			.sh-channel-dd-container {
				margin: 0px 4px;

				.ember-basic-dropdown {
					width: 200px;
					.ember-basic-dropdown-content {
						position: relative;
					}
				}
			}

			.ui-dropdown-grouped .ember-basic-dropdown-content {
				width: 400px;

				.group-options .ember-power-select-options {
					max-height: 264px;
				}
			}
		}
	}
}
.on-call-schedule-container, .on-call-shift-container{
	@each $color in $shiftColors {
		$i: index($shiftColors, $color)-1;
		.bg-color-#{$i}{
			background-color: $color !important;
		}
		.shift-col-#{$i}{
			background-color: #{$color}4D;
			&.override{
				background: repeating-linear-gradient(135deg,transparent,transparent 14px,#{$color}4D 10px, #{$color}4D 20px), linear-gradient(to bottom, #{$color}80, #{$color}80)
			}
		}
		.border-col-#{$i}{
			border-color: $color !important;
		}
	}
}


