.wlm-admin-page {
	.page-layout-content {
		background: $app-white;
	}
}

.workload-admin-header-section {
	@include flexbox-simple(space-between, center);
	border-bottom: 1px solid $app-grey-bg-2;
	height: 56px;
	margin: -16px;



	.heading {
		font-size: $title-normal;
		margin-left: 24px;
		margin-bottom: 0;
		font-weight: $text-weight-semi-bold;
	}

	.heading-right {
		margin-right: 10px;

		button {
			width: 90px;
		}

		.learn-more {
			font-weight: $text-weight-semi-bold;
			margin-right: 16px;
		}
	}
}

.workload-admin-section {
	margin: -16px;

	.admin-workload-title {
		margin-bottom: 4px !important;
	}

	.admin-card {
		margin: 40px 32px 24px 32px;
		border: 1px solid $border-light-color;
		border-radius: 8px;

		.admin-workload {
			@include flexbox-simple(flex-start, flex-start);

			.admin-contents {
				margin-left: 25px;

				.title {
					font-weight: $text-weight-bold;
				}

				.content {
					.line-item {
						@include flexbox-simple(flex-start, baseline);

						.item {
							margin-left: 11px;
							width: 427px;
						}
					}
				}
			}
		}
	}


	.card {
		border-radius: 4px;
		box-sizing: border-box;
		box-shadow: $card-box-shadow;
		padding: 20px 24px;
		width: auto;
		background: none;
	}

	.break-section {
		margin: 6px 32px 0 32px;
	}

	.allocations-box {
		padding: 24px 32px;

		.allocation-card {
			border: 1px solid $border-light-color;
			border-radius: 8px;
			margin-top: 8px;

			.allocation-contents {
				width: 100%;

				hr {
					margin: 16px 0 !important;
				}

				.allocation-title {
					@include flexbox-simple(flex-start, center);
					font-size: $title-normal;
					line-height: 20px;
					font-weight: $text-weight-semi-bold;
					margin-bottom: 12px;

					.allocation-tooltip {
						@include flexbox-simple(flex-start, center);
						margin-left: 6px;
					}

					.allocation-level-icon {
						height: 20px;
						width: 20px;
						border-radius: 50%;
						margin-right: 8px;
						display: inline-block;

						&-yellow {
							background-color: $app-casablanca-light;
							border: 1px solid $alertPartialBorder;
						}

						&-red {
							background-color: $alertWarningBackground;
							border: 1px solid $alertWarningBorder;
						}

						&-blue {
							background-color: $optimally-allocated-background;
							border: 1px solid $optimally-allocated-border;
						}
					}
				}

				.content {
					@include flexbox-simple(flex-start, center);

					.form-fields {
						@include flexbox-simple(flex-start, center);
						margin-left: 12px;
					}

					.form-field--label {
						display: none;
					}

					.allocation-dd-field {
						width: 200px;
						height: 32px;
					}

					.allocation-number-field {
						margin-left: 8px;
						margin-right: 8px;
						width: 55px;
					}

					.errors {
						margin-left: 12px;
						color: $app-red;
						font-size: $text-normal;
						margin-bottom: 0;
					}
				}

				hr:last-child {
					display: none;
				}
			}
		}

		.text-contents {
			width: 600px;

			.info-content {
				border-radius: 4px;
				border: 1px solid $alertInfoBorder;
				background: $alertInfoBackground;
				@include flexbox-simple(flex-start, center);

				.alert-icon {
					@include flexbox-simple(flex-start, flex-start);

					svg {
						margin: 10px 8px;
					}

					border-right: 1px solid $border-light-blue;
				}

				.content-area {
					margin-left: 12px;
				}
			}
		}
	}

	.roles-section {
		padding: 24px 32px;
		display: flex;
		justify-content: space-between;

		.roles-content {
			margin-left: 20px;

			.line-item {

				@include flexbox-simple(flex-start, center);

				.item {
					margin-left: 11px;
					margin-bottom: 0px;
				}

				.role-hint-text {
					vertical-align: middle;
					margin-left: 8px;
					top: 3px;
				}
			}
		}

		.view-roles {
			@include flexbox-simple(flex-start, center);
		}
	}
}
