// Business Object Field Create/Edit Modal
.bo-field-modal {
	.form-fields {
		.form-field {
			margin-top: 20px;
			&.text-field label {
				display: block;
				margin-bottom: 5px;
			}
			input[type='text'] {
				width: 100%;
			}
			.star {
				color: $app-red;
			}
			.field-dropdown {
				width: 320px;
				margin-bottom: 20px;
				&.disabled {
					.ember-power-select-selected-item {
						color: $btnDisabledColor;
					}
					.ember-power-select-status-icon {
						border-color: $btnDisabledColor;
					}
				}
				.ember-power-select-selected-item {
					display: flex;
					width: calc(100% - 25px);
					.select-item {
						display: flex;
						width: 100%;
						.dependent {
							padding: 0 10px;
							height: 22px;
							font-size: $text-small;
							align-self: center;
						}
					}
				}
				.select-item {
					display: flex;
					.item-label {
						flex: 1;
					}
					.dependent {
						padding: 1px 10px;
						background-color: $background-color-2;
						border-radius: 10px;
						font-size: 12px;
						color: $disabled-text-color;
					}
				}
				&.sub-level {
					position: relative;
				}
			}
			.catalog-field {
				position: relative;
				margin-left: 25px;
				margin-top: 25px;
				&:before {
					content: '';
					position: absolute;
					height: 85px;
					top: -20px;
					left: -20px;
					border: 0.5px dotted $border-dark-color;
				}
				.field-dropdown {
					width: 295px;
				}
			}
			label.tile-error, label.dropdown-error {
				color: $app-red;
				margin-top: -10px;
			}
		}
		.bo-field-name.error {
			color: $btnPrimaryBorder;
			input {
				border: 1px solid $app-red;
			}
			label.error {
				color: $app-red;
				margin-top: 10px;
			}
		}
	}
}


.bo-field-types.field-type-wrapper {
	display: flex;
	margin-bottom: 20px;
	margin-top: 5px;
	flex-wrap: wrap;
	.field-type {
		width: 100px;
		height: 70px;
		border: 1px solid $btnBorder;
		border-radius: 5px;
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		font-size: 12px;
		cursor: pointer;
		color: $btnPrimaryBorder;
		&.error {
			border: 1px solid $app-red;
		}
		&:hover {
			background-color: $app-sidebar-bg;
		}
		&:not(:last-child) {
			margin-right: 7px;
			margin-bottom: 10px;
		}
		.field-icon {
			width: 22px;
			height: 22px;
			margin-bottom: 5px;
			margin-right: 5px;
		}
		.type-label {
			text-transform: capitalize;
		}
		&.selected {
			position: relative;
			background-color: $dropdown-option-selected;
			border-color: $light-blue-border;
			.approve {
				position: absolute;
				top: 5px;
				left: 5px;
				height: 15px;
				width: 15px;
				path {
					fill: $inputFocusBorder;
				}
			}
		}
		&.disabled {
			opacity: 0.6;
		}
	}
}