&.alerts_admin_itom_monitoring_tools.page-layout__page-wrapper {
	.page-layout-content {
		.page-content {
			padding: 0;

			.tabs .tabs__items {
				border-bottom: 2px solid $border-light-color;
			}
		}

		.page-sidebar {
			height: auto;
		}
	}
}

.activity-logs-container {
	width: 75%;
	margin-left: 40px;
	margin-top: 42px;

	.fs-tl-container {
		.fs-tl {
			padding: 0;

			.fs-tl-item {
				list-style: none;
				margin: auto;
				margin-left: 184px;
				min-height: 32px;
				border-left: 2px solid $border-light-color;
				padding: 0 0 28px 42px;
				position: relative;

				&:last-child {
					border-left: 0;
				}

				&.fs-tl-more:last-child {
					border-left: 2px solid $border-light-color;
				}

				&.fs-tl-item-marker {
					.fs-tl-item-time {
						font-weight: $text-weight-semi-bold;
						top: -4px;
					}

					.fs-tl-item-icon.fs-tl-item-icon-empty {
						top: 0px;
						background: $app-warning-state;
						width: 10px;
						height: 10px;
						left: -6px;
						border-radius: 50%;
					}
				}

				.fs-tl-item-time {
					position: absolute;
					left: -205px;
					text-align: right;
					color: $app-grey;
					min-width: 172px;
				}

				.fs-tl-item-icon {
					position: absolute;
					left: -22px;
					top: -12px;

					&.fs-tl-item-icon-empty {
						top: 12px;
						background: $secondary-text-color-2;
						width: 10px;
						height: 10px;
						left: -6px;
						border-radius: 50%;
					}

					.avatar__icon {
						background: $app-white;
						border: 1px solid $highlight-grey;
						height: 42px;
						width: 42px;
						border-radius: 50%;
						@include flexbox-simple(center, center);


						svg {
							height: 20px;
							width: 20px;
						}

						img {
							height: 50%;
							width: 50%;
							border-radius: 0%;
						}
					}

					&.custom-tool>.avatar__icon {
						background: $app-icon-color;
					}
				}

				.fs-tl-item-body {
					.fs-tl-item-title {
						font-weight: $text-weight-medium;
						margin-bottom: 8px;

						a {
							color: $app-grey;
						}
					}

					.fs-tl-item-desc {
						background: $icon-background;
						border: 1px solid $border-light-color;
						box-sizing: border-box;
						border-radius: 4px;
						padding: 12px 12px 4px 16px;
						font-weight: $text-weight-medium;

						b {
							color: $app-grey;
						}

						h2 {
							text-transform: capitalize;
						}

						.list-title {
							margin-bottom: 12px;

							&:not(:first-child) {
								margin-top: 16px;
							}
						}

						.list-items {
							li {
								padding: 2px 0px;
								margin-bottom: 4px;

								&::before {
									content: "\2022"; // Circle bullet charactor
									color: $secondary-text-color-2;
									display: inline-block;
									width: 16px;
									font-size: 16px;
									line-height: 24px;
									font-weight: $text-weight-bold;
								}
							}
						}
					}

					.fs-tl-item--name {
						margin-bottom: 24px;
					}

					.fs-tl-item--error-desc,
					.fs-tl-item--log-desc {
						color: $toggleSlider;
						margin: 4px 0;
					}
				}
			}
		}
	}
}

.no-activites {
	@include flexbox-simple(space-between, center);
	flex-direction: column;
	height: 260px;
	margin: 50px 0;

	&>img {
		width: 200px;
		height: 200px;
	}

	.no-activities-helptext {
		font-weight: $text-weight-medium;
		width: 46%;
		text-align: center;
	}
}

.debugError__btn-link {
	background: transparent;
	border: none;
	padding: 0;
	cursor: pointer;
	color: $linkColor;
	font-weight: $text-weight-bold;
}

.activity-logs-tl-item--name {
	margin-bottom: 18px;
	font-weight: $text-weight-medium;
	color: $app-grey;
	text-transform: capitalize;
	font-size: 14px;

	&-error {
		margin-bottom: 12px;

		&>span.ember-badge {
			margin-left: 10px;
		}
	}
}

.activity-logs-tl-item--error-title {
	font-weight: $text-weight-semi-bold;
	color: $app-grey;
	font-size: 14px;
	margin-bottom: 2px;
	line-height: 21px;
}

.activity-logs-tl-item--error-desc,
.activity-logs-tl-item--log-desc {
	color: $toggleSlider;
	line-height: 21px;
}

.monitoring__tools-container {
	min-height: 768px
}