.update-agent-ph-container {
	.alert-icon {
		svg {
			fill: $btnDangerBorder;
		}
	}

	.update-ph-notification-summary {
		color: $secondary-text-color-2;
		line-height: 20px;

		p {
			.channel-link {
				font-weight: $text-weight-semi-bold;
				cursor: pointer;
				svg{
					fill: $linkColor;
					width: 10px;
					height: 10px;
					margin: 0 4px;
				}
			}
		}

		h3 {
			font-weight: $text-weight-medium;
			margin-bottom: 8px;
		}

		ul {
			list-style: disc;
			margin-top: 4px;
			li {
				margin-left: 20px;
				.channel-link {
					font-weight: $text-weight-semi-bold;
					cursor: pointer;
					svg {
						fill: $linkColor;
						width: 14px;
						height: 14px;
						margin: -1px 2px;
					}
				}
			}
		}
	}
	.agent-list {
		border-radius: $radius;
		border: 1px solid $border-light-color;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
		padding: 12px 20px 12px 20px;
		margin-top: 16px;

		.agent-name-container {
			@include flexbox;
			@include align-items(center);
			margin-bottom: 12px;

			.agent-name {
				@include font-size($title-normal);
				font-weight: $text-weight-medium;
				color: $primary-text-color-1;
				margin-left: 8px;
				white-space: nowrap;
			}
		}

		.ph-number-container{
			@include flexbox;
			@include justify-content(space-between);
		}
	}
	.update-contact-notice	{
		white-space: pre-line;
	}
}
