.goal-selection-explore{
	min-height: calc(100vh - 67px);
	position: absolute;
	left: 0;
	top: 0;
	background: $app-white;
	width: 100%;

	.not-found-page{
		@include flex-center;
		flex-direction: column;
		position: absolute;
		transform: translate(-50%, -50%);
		top: 50%;
		left: 50%;
		.empty-message{
			font-weight: 600;
			font-size: 18px;
			text-align: center;
		}
		.btn-primary{
			display: block;
			margin: 30px auto;
		}
	}
	.expore-head{
		padding: 48px;
		font-style: normal;
		font-weight: $text-weight-semi-bold;
		font-size: $font-icon-large;
		line-height: 36px;
		text-align: center;
		color: $app-white;
	}

	.explore-content{
		background-color: $app-white;
		border-radius: 16px;
		.btn-primary{
			padding-left: 25px;
			padding-right: 25px;
			margin-top: 40px;
		}
	}

	.goals-content{
		background: $goalBackground;
		height: 488px;
	}

	.content-box{
		width: max-content;
		height: 498px;
		position: absolute;
		left: 50%;
		transform: translate(-50% , 0);
		border: 1px solid $app-border-primary;
		box-shadow: 0px 12px 18px rgba($app-black, 0.05);
		border-radius: 16px;
		padding: 48px 44px;
	}

	.explore-list{
		width: 400px;
	}
	.explore-item{
		position: relative;
		border: 1px solid $inputBorder;
		box-shadow: 0px 0px 6px $box-shadow-color-1;
		border-radius: 4px;
		background: $app-white;
		padding: 12px 18px;
		margin-bottom: 12px;
		box-sizing:border-box;
		width: 100%;
		text-align: left;
		cursor: pointer;
		&:hover{
			&:not(.default){
				border-color: $btnDisabledColor;
			}
			.goal-selection{
				color: $btnDisabledColor;
			}
		}
		&.selected{
			&.default{
				.goal-selection{
					color: rgba($app-success-state, 0.5);
				}
				.goal-title{
					color: $primary-text-color-1;
				}
			}
			.goal-selection{
				color: $app-success-state;
			}
		}
		.goal-selection{
			position: absolute;
			top: 10px;
			left: -12px;
			font-size: $title-extra-large;
			color: $inputBorder;
			&:after{
				content: '';
				display: block;
				width: 22px;
				height: 22px;
				border-radius: 50%;
				background: $app-white;
				margin-top: -24px;
			}
			.disabled{
				opacity: .5;
			}
		}
		.goal-title{
			font-size: $title-small;
			font-weight: 500;
			cursor: pointer;
			margin-bottom: 3px;
		}
		.goal-info{
			color: $inputHoverBorder;
			font-size: $text-normal;
			margin: 0;
		}

	}

	.goal-image{
		background: center / contain no-repeat url("/a/assets/images/thp/goal-selection/improve_customer_experience.png");
		width: 616px;
		height: 349px;
		margin-left: 20px;
		margin-top: 20px;
		&.manage_asset_lifecycle{
			background-image: url("/a/assets/images/thp/goal-selection/manage_asset_lifecycle.png");
		}
		&.it_productivity{
			background-image: url("/a/assets/images/thp/goal-selection/it_productivity.png");
		}
		&.optimize_it_operations{
			background-image: url("/a/assets/images/thp/goal-selection/optimize_it_operations.png");
		}
	}
}
