@import "ams-common";

// Alert severity states
$severities: 'ok', 'warning', 'error', 'critical';

$table-row-types: 'header', 'item';

// List page alerts table column width settings
$list-table-cols: 'check-box', 'id', 'message', 'severity', 'state', 'updated-date', 'created-date', 'incident', 'business-service', 'tags', 'host', 'profile', 'integration', 'asset', 'acknowledged-by';
$list-table-col-widths: 14px, 96px, 267px, 77px, 60px, 228px, 228px, 136px, 152px, 152px, 267px, 200px, 200px, 200px, 200px;

// Details page log table column width settings
$logs-table-cols: 'date', 'message', 'severity', 'value';
$logs-table-col-widths: 180px, 300px, 180px, 300px;

// Placeholder selectors for severity states
%bg-severity-ok { background-color: $ok-state; }

%bg-severity-warning { background-color: $warning-state; }

%bg-severity-error { background-color: $error-state; }

%bg-severity-critical { background-color: $critical-state; }

// Setting severity color tags : <state-color> <state-text>
@mixin set-severity-tags() {
	@each $severity in $severities {
		.state-#{$severity} .tag {
			width: 8px;
			height: 8px;
			display: inline-block;
			border-radius: 2px;
			margin-right: 5px;
			@extend %bg-severity-#{$severity};
		}
	}
}

// Setting widths of table's each column header cells and row item cells
@mixin set-table-cell-widths($cols, $col-widths) {
	@each $type in $table-row-types {
		@each $col in $cols {
			$i: index($cols, $col);
			.cell-alert-#{$col}-#{$type} {
				width: nth($col-widths, $i);
				box-sizing: content-box;
				text-transform: none;
			}
		}
	}
}

@mixin set-list-table-cell-widths() {
	@include set-table-cell-widths($list-table-cols, $list-table-col-widths);
}

@mixin set-logs-table-cell-widths() {
	@include set-table-cell-widths($logs-table-cols, $logs-table-col-widths);
}

html {
	scroll-behavior: smooth;
}

.hide {
	display: none !important;
}

// Common styles for the Bulk actions section
// ('Associate', 'Mark as Resolved') in alert list and details page
.bulk-action-options {
	display: inline-flex;
	margin-right: 6px;

	.associate-grp {
		align-items: center;
		display: flex;
		justify-content: space-around;
		user-select: none;
	}

	.ember-basic-dropdown {
		svg {
			height: 16px;
			padding-left: 0px;
			width: 16px;
		}

		&.disabled {
			.associate-grp svg { fill: $disabled-text-color; }
		}

		.ember-basic-dropdown-content {
			min-width: 176px;

			.basic-dropdown-item {
				font-weight: 500;

				&.disabled {
					color: $disabled-text-color;
					cursor: default;
				}
			}
		}
	}
}

// Spinner container
.loader-container {
	align-items: center;
	display: flex;

	svg {
		fill: $app-primary;
		height: 28px;
		width: 28px;
	}
}

