
// Business Object Record Create/Edit Modal
.bo-record-modal {
	.ember-modal-body {
		.field {
			margin-bottom: 20px;
			.star {
				color: $app-red !important;
			}
			input:not(.date-text-field):not(.ember-power-select-search-input) {
				width: 530px;
			}
			input[type="search"]:not(.ember-power-select-search-input) {
				width: 512px;
				margin-bottom: 0;
			}
			.dependent-field-wrapper {
				padding: 15px 20px 10px;
				background: $app-sidebar-bg;
				.dependent-select-dropdown-field {
					.form-field--label {
						display: none;
					}
					.ember-power-select-trigger {
						margin-bottom: 15px;
						.ember-power-select-status-icon {
							left: 96%;
						}
					}
					input[type="search"] {
						width: 470px;
					}
				}
				.ember-view {
					position: relative;
					.dependent-select-dropdown-field {
						margin-left: 25px;
						margin-top: 20px;
						input[type="search"] {
							width: 445px;
						}
					}
				}
			}
			.ember-view { 
				.date-control {
					input:focus {
						border: 2px solid $inputFocusBorder;
						box-shadow: none;
						transition: 0s;
					}
					input[readonly] {
						background-color: $white !important;
					}
					input[type="search"] {
						width: 90px;
					}
				}
			}
			.autocomplete-field {
				.ember-power-select-status-icon {
					display: none;
				}
			}
			label.error {
				color: $app-red !important;
				font-size: 12px;
				margin-top: 5px;
				margin-left: 2px;
				&.dependent {
					margin-top: -25px;
					margin-left: 25px;
				}
			}
			.field-dropdown {
				&.location {
					.ember-power-select-option {
						.level-0 { padding-left: 0px ; }
						.level-1 { padding-left:15px ; }
						.level-2 { padding-left:30px ; }
						.level-3 { padding-left:45px ; }
						.level-4 { padding-left:60px ; }
					}
				}
			}
			&.disabled input {
				color: $disabled-text-color;
				background-color: $inputDisabledBackground;
				border-color: $border-light-color !important;
				pointer-events: none;
				cursor: default;
			}
		}
	}
}

.bo-records {
	margin-top: 20px;
	.records-nav {
		height: 30px;
		margin-left: 15px;
		button.btn-link {
			position: relative;
			font-weight: $text-weight-semi-bold;
			font-size: $title-small;
			float: right;
			margin-right: 16px;
			bottom: 3px;
			outline: none;
		}
		
		.add-btn {
			position: relative;
			margin-right: 10px;
			float: right;
			padding-right: 15px;

			.add-records-field {
				color: $app-black;
				font-weight: 500;

				.new-field {
					vertical-align: middle;
					margin-right: 5px;
					height: 16px;
					width: 16px;
				}
				&:before {
					position: absolute;
					content: '';
					top: -3px;
					right: 0;
					width: 0;
					height: 25px;
					border-left: 1px solid rgba($app-black,0.1);
				}
			}
		}
		.page-nav-wrap {
			margin-top: -5px;
		}
	}
}

// Records table
.bo-records-table {
	margin-top: 10px;
	.lt-body {
		.lt-row {
			.lt-cell:not(:first-child) {
				border-right: 1px solid $border-dark-color !important;
			}
			td.lt-cell:first-child {
				padding-left: 15px !important;
			}
		}
		.rule-cell {
			height: 100%;
			display: flex;
			align-items: center;
		}
	}
}
#bo-import-info{
	.import-info{
		list-style: disc;
		padding-left: 20px;
		> li{
			padding-bottom: 8px;
		}
	}
}