&.alerts_admin_itom_alert_integrations_new_onboarding.page-layout__page-wrapper {
	.page-content {
		margin: 0;
		border-radius: 0;
		height: calc(100vh - 60px);

		.new-onboarding-content {
			@include flexbox;
			@include justify-content(space-evenly);
			margin: 50px 100px;
			gap: 100px;

			.svg-ams-illustration {
				width: 100%;
				height: 270px;
			}
		}

		.ams-title {
			@include font-size($title-extra-large);
			margin-top: 18px;
		}

		.ams-description {
			@include font-size($title-small);
			color: $primary-text-color-1;
			margin-top: 8px;
			width: 64%;
		}

		.list-wrapper {
			margin-top: 24px;
		}
	}
}
