.integration-list {
	clear: both;
	min-height: 382px;
	padding-top: 16px;
	.alert-integration-accordion {
		margin-bottom: 16px;
		.integration-list-item {
			background: $app-white;
			border: 1px solid $border-light-color;
			border-radius: 4px;
			box-shadow: 0 1px 6px rgba(22, 29, 37, 0.05);
			box-sizing: border-box;
			cursor: pointer;
			@include flexbox;
			padding: 12px;
			margin-bottom: 0;
			height: 72px;

			&:hover {
				background: $background-color-2;
				border: 1px solid $border-light-blue;
				border-radius: 4px;
				box-shadow: 0 1px 6px rgba(22, 29, 37, 0.05);
				box-sizing: border-box;
			}

			.content-link {
				cursor: pointer;
				width: calc(100% - 188px);
				height: 48px;

				&.with-service-map {
					width: calc(100% - 244px);
				}

				.logo-section {
					float: left;
					width: 48px;
					height: 48px;
					background: $app-white;
					border: 1px solid $border-light-color;
					border-radius: 4px;
					margin-right: 16px;
					text-align: center;
					@include align-items(center);
					@include justify-content(center);
					@include flexbox;
					img {
						width: 24px;
						height: 24px;
					}
					&.custom-integration, &.email {
						.logo-wrapper {
							padding: 4px;
							background: $app-icon-color;
							border-radius: 50%;
							@include flexbox;
						}
					}
				}

				.content {
					width: auto;
					height: 48px;
					@include align-items(flex-start);
					color: $primary-text-color-1;
					@include flexbox;
					@include flex-direction(column);
					@include justify-content(center);
					.name {
						font-size: $title-small;
						font-weight: $text-weight-semi-bold;
						max-width: 90%;
					}
					.meta {
						color: $secondary-text-color-1;
						font-size: $text-normal;
						font-weight: normal;
						padding-top: 8px;
						@include flexbox;
						@include align-items(center);
						a {
							font-weight: $text-weight-semi-bold;
							margin-right: 4px;
						}
						.service-status {
							margin: 0 12px;
							padding: 0 8px;
							border-left: 1px solid $primary-text-color-1;
							@include flexbox;
							@include align-items(center);
							b {
								font-weight: $text-weight-semi-bold;
								overflow: hidden;
								display: inline-block;
								white-space: nowrap;
								text-overflow: ellipsis;
								margin-left: 4px;
								max-width: 300px;
							}
						}
						.btn-link {
							border: 0;
							height: 16px;
							margin: 0 12px 2px;
							padding: 0 8px;
							border-left: 1px solid $primary-text-color-1;
							@include font-size(12px);
							color: $linkColor !important;
							&:hover {
								border-left: 1px solid $primary-text-color-1 !important;
							}
							&:focus {
								box-shadow: none;
							}
						}
					}
				}
			}

			.detach-service, .map-service, .edit-integration {
				margin: auto 0 auto 16px;
				padding: 4px;
				width: 32px;
				height: 32px;
				@include flexbox;
				@include align-items(center);
				@include justify-content(center);
				svg {
					width: 16px;
					height: 16px;
				}
			}
			
			.edit-integration svg {
				width: 12px;
				height: 12px;
			}

			.toggle-switch-container {
				margin: auto 32px auto 16px;
				height: 20px;

				.toggle-switch {
					cursor: pointer;
					
					&.disabled {
						.slider {
							background-color: $radioDisabledBorder;

							&:before { border-color: $radioDisabledBorder; }

							svg { fill: $radioDisabledBorder; }
						}
					}
				}
			}

			.info-tooltip-container{
				padding: 8px
			}

			.toggle-icon {
				@include flexbox;
				border-left: 1px solid $background-color;
				cursor: pointer;
				svg {
					margin: auto 8px auto 24px;
					width: 32px;
					height: 32px;
				}
			}
		}
		
		.integration-details-wrapper {
			.authorization-header {
				padding: 16px 0 8px;
				border: 1px dashed $border-dark-color;
				border-top: none;
				border-bottom-left-radius: 4px;
				border-bottom-right-radius: 4px;
				@import "manage-integrations/authorization";
				.next-step-icon {
					margin: -4px 0 -8px -2px;
				}
			}
		}
	}
	.separtor {
		margin-right: 12px;
		width: 1px;
		background-color: $app-grey;
		height: 16px;
	}
}
