.alert-aggregation {
	margin: 16px 48px;
	.title {
		@include font-size($title-normal);
		font-weight: $text-weight-semi-bold;
		margin-bottom: 16px;
		margin-left: 4px;
	}
	.aggregation-content {
		padding: 32px 24px;
		border: 1px dashed $inputBorder;
		border-radius: $radius;
		.aggregation-label {
			margin-bottom: 12px;
		}
		.aggregation-selector {
			width: 560px;
			position: relative;
			.field-error {
				margin-top: 8px;
				@include font-size($text-normal);
				color: $app-red-1;
			}
			.add-btn {
				position: absolute;
				border: 1px solid $primary-border;
				border-radius: 0 2px 2px 0;
				color: $linkColor;
				cursor: pointer;
				@include font-size($title-small);
				height: 32px;
				line-height: 1;
				outline: 0;
				right: 0;
				top: 0;
				padding: 0;
				&.hide {
					opacity: 0;
				}
				&:hover {
					opacity: 1;
					background-color: $app-primary;
					svg {
						path {
							&:first-child {
								fill: $app-primary;
								stroke: $app-primary;
							}
							&:last-child {
								fill: $app-white;
							}
						}
					}
				}
				&.disabled {
					background: $border-light-color;
					color: $btnDisabledColor;
					cursor: not-allowed;
					pointer-events: auto;
					svg {
						path {
							&:first-child {
								fill: $btnDisabledBackground;
								stroke: $btnDisabledBackground;
							}
							&:last-child {
								fill: $disabled-text-color;
							}
						}
					}
				}
				svg {
					width: 32px;
					height: 32px;
				}
			}
			.alert-aggregation-modal {
				background-color: $white;
				border-radius: $radius;
				box-shadow: 0 1px 5px 0 $toggleSlider;
				left: 100%;
				margin-left: 16px;
				position: absolute;
				z-index: 15;
				right: 0;
				bottom: 0;
				width: 376px;
				.ember-modal-header {
					padding: 16px;
					.title {
						font-weight: $text-weight-semi-bold;
						@include font-size($title-normal);
						margin-bottom: 0;
					}
					.ember-modal-close {
						cursor: pointer;
						float: right;
						padding-top: 4px;
						background: 0 0;
						border: 1px solid transparent;
						svg {
							fill: $app-muted-color;
							height: 14px;
							width: 14px;
						}
					}
				}
				.ember-modal-body {
					padding: 0 16px 16px;
					.subtitle {
						@include font-size($text-normal);
					}
					.labels-wrapper {
						margin-top: 8px;
						max-height: 160px;
						overflow: auto;
						.aggregation-label {
							background: $white;
							border: 1px solid $primary-border;
							border-radius: 2px;
							box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
							cursor: pointer;
							min-height: 32px;
							margin: 0 0 8px;
							padding: 6px 16px;
							width: 100%;
							text-align: left;
							word-break: break-word;
							&:hover {
								background: $background-color-2;
							}
						}
					}
				}
			}
		}
		.fs-multiple-options-content {
			width: 560px;
			.fs-multiple-option {
				margin: 2px;
			}
		}
		.field-desc {
			margin-top: 8px;
			@include font-size($text-normal);
			font-weight: $text-weight-normal;
			color: $secondary-text-color-2;
		}
	}
}
