.ocs-breadcrumb{
	margin-left: 32px;
}

.on-call-schedule-list {
	background-color: $white !important;
	
	&.page-layout__page-wrapper .page_layout__title-bar .sticky-element {
		padding-left: 32px;
	}

	.page-content {
		margin: 24px 0;
	}
	.ember-power-select-multiple-option {
		color: $primary-text-color-1;
	}
	.page-title {
		.ember-breadcrumbs {
			display: block;
			.title-info {
				font-weight: $text-weight-normal;
				font-size: 12px;
			}
		}
	}
	.schedule-list {
		.schedule-item {
			margin: 12px;
			border-radius: 8px;
			display: flex;
			border: 1px solid $background-color;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
			min-height: 70px;
			box-sizing: border-box;
			padding: 12px 16px;
			&:hover{
				border-color: $border-light-blue;
			}
			.schedule-name-container{
				@include flexbox;
				width: 75%;
				margin-top: 8px;
				.schedule-item-info {
					font-weight: $text-weight-medium;
					@include font-size($baseFontSize);
					color: $app-primary;
					word-break: break-word;
					margin-right: 8px;	
				}
				.schedule-item-invalid-notification {
					margin-top: 4px;
					svg {
						fill: $btnDangerBorder;
						width: 16px;
						height: 16px;
						cursor: pointer;
					}
					.agent-tooltip-container {
						padding: 8px 10px;
					}
				}
			}
			.schedule-item-details {
				width: 25%;
				min-width: 200px;
				border-left: 1px solid $background-color;
				padding: 4px 12px;
				.schedule-group {
					display: flex;
					label {
						min-width: 60px;
					}
					.group-name {
						font-weight: $text-weight-medium;
						word-break: break-all;
					}
				}
				.calendar-link {
					@include font-size(14px);
					font-weight: $text-weight-medium;
				}
			}
		}
	}
	.ember-pagination-new{
		border-top: none;
	}
	.no-schedule-container{
		@include flexbox-simple(center, center);
		margin-top: 64px;
		.no-schedule-info {
			max-width: 520px;
			padding: 0 30px;
			h2 {
				@include font-size(24px);
				color: $primary-text-color-1;
			}

			.no-schedule-info-list {
				@include font-size(14px);
				color: $secondary-text-color-1;
				margin: 16px 0 28px 8px;
				li {
					padding: 2px 0px;
					margin-bottom: 2px;

					&::before {
						content: "\2022"; // Circle bullet charactor
						color: $secondary-text-color-2;
						display: inline-block;
						width: 16px;
						font-size: 16px;
						line-height: 16px;
						font-weight: 700;
					}
				}
			}
		}
	}
}
.ember-modal-overlay .ember-modal-dialog.schedule-confirmation-modal {
	top: 200px;
	.ember-modal-header {
		.title {
			font-size: $text-large;
			font-weight: $text-weight-semi-bold;
		}
	}
	.ember-modal-body {
		max-height: fit-content;
	}

	.ember-modal-footer {
		.btn {
			min-width: 60px;
		}
	}
}
