#timeline{
	background: $app-white;
	padding: 0;
	#timeline-info{
		display: flex;
		margin: 16px 16px 0;
		padding-top: 16px;
		border-top: 1px solid $border-light-blue;
		font-size: 14px;
		#timeline-range{
			display: inline-block;
			flex-grow: 1;
			padding: 16px;
			font-weight: $text-weight-medium;
		}
		#timeline-views{
			display: flex;
			margin: 8px;
			border-radius: 4px;
			background: linear-gradient(180deg, $app-white 2.56%, $background-color-2 95.75%), $app-primary;
			span{
				cursor: pointer;
				padding: 8px;
				color: $app-grey;
				border: 1px solid $app-border-primary;
				border-collapse: collapse;
				text-transform: capitalize;
				&:first-child{
					border-top-left-radius: 4px;
					border-bottom-left-radius: 4px;
				}
				&:last-child{
					border-top-right-radius: 4px;
					border-bottom-right-radius: 4px;
				}
				&.active{
					background: $app-primary;
					color: $app-white;
					border-color: $border-light-blue;
				}
			}   
		}
		#timeline-controls{
			display: flex;
			margin: 8px;
			background: linear-gradient(180deg, $app-white 2.56%, $background-color-2 95.75%), $app-primary;
			border-radius: 4px;
			border: 1px solid $app-border-primary;
			span{
				cursor: pointer;
				padding: 8px;
				color: $app-grey;
				svg{
					height: 16px;
					width: 16px;
					vertical-align: middle;
				}
				&:first-child{
					border-right: 1px solid $border-light-color;
				}
			}
		}
		&.loading{
			#timeline-views>span, #timeline-controls>span{
				opacity: 0.8;
				pointer-events: none;
			}
		}
	}
	#timeline-main{
		margin: 16px;
		#timeline-header{
			display: flex;
			width: 100%;
			position: relative;
			padding-left: 150px;
			border-radius: 4px;
			font-weight: $text-weight-semi-bold;
			background: $section-background-blue;
			height: 44px;
			.timeline-column{
				display: inline-block;
				min-width: 45px;
				flex-grow: 1;
				text-align: center;
				padding: 4px;
				font-size: $text-normal;
				font-weight: $text-weight-semi-bold;
				background: $section-background-blue;
				&.timeline-time{
					text-align: left;
					padding-top: 12px;
				}
				&:first-child{
					border-top-left-radius: 4px;
					border-bottom-left-radius: 4px;
				}
				&:last-child{
					border-top-right-radius: 4px;
					border-bottom-right-radius: 4px;
				}
			}
		}
		#timeline-layer{
			.layer{
				border: 1px solid $border-light-blue;
				box-sizing: border-box;
				border-radius: 4px;
				margin: 8px 0;
				height: 40px;
				font-size: 14px;
				.layer-name{
					width: 140px;
					font-weight: $text-weight-semi-bold;
					display: inline-block;
					top: -15px;
					left: 12px;
					position: relative;
				}
				.layer-schedule{
					display: inline-flex;
					width: calc(100% - 145px) !important;
					height: 38px;
					position: relative;
					.column-grid{
						flex-grow:1;
						border-left: 1px solid $border-light-blue;
					}
					.layer-loading{
						.shimmer-loader-item{
							display: inline-block;
							width: calc(100% - 16px);
							margin: 12px;
						}
					}
					.shift-event-box{
						position: absolute;
						box-sizing: border-box;
						height: 38px;
						.shift-event{
							display: block;
							cursor: pointer;
							height: calc(100% - 8px);
							margin: 4px 0;
							box-sizing: border-box;
							padding: 4px 2px;
							border: 1px solid;
							border-radius: 4px;
							text-overflow: ellipsis;
							overflow: hidden;
							white-space: nowrap;
						}
						&.old-shift{
							.shift-event{
								background: $app-border-primary !important;
								border-color: $app-border-primary !important;
								color: $disabled-text-color;
							}
						}
						&.right-align{
							.ember-tooltip{
								right: 0 !important;
								left: auto !important;
								transform: none !important;
								.ember-tooltip-arrow{
									right: 0;
									left: auto !important;
								}
							}
						}
					}
				}
			}
		}
		.ember-tooltip{
			border: 1px solid $dropdown-content-border;
			box-shadow: 0px 2px 4px rgba(18, 52, 77, 0.06), 0px 2px 16px rgba(18, 52, 77, 0.16);
			text-shadow: none;
			background: $app-white !important;
			max-width: 350px;
			width: 350px;
			user-select: auto;
			pointer-events: all;
			padding: 2px;
			color: $app-grey !important;
			bottom: 30px;
			left: 50% !important;
			transform: translateX(-50%) !important;
			top: auto !important;
			.ember-tooltip-arrow{
				border-top-color: $app-white;
			}
			.event-profile{
				display: flex;
				line-height: 20px;
				border-left: 5px solid;
				border-radius: 3px;
				padding: 16px 9px;
				.profile-img{
					width: 40px;
					margin-right: 9px;
				}
				.profile-info{
					flex-grow: 1;
					.profile-name{
						font-weight: $text-weight-semi-bold;
						font-size: $title-small;
						color: $app-grey;
						display: block;
						width: calc(100% - 50px);
						word-break: break-word;
						line-height: 17px;
						padding: 6px 0 2px;
					}
					.profile-email, .profile-phone{
						font-weight: $text-weight-normal;
						font-size: $text-normal;
						color: $app-primary;
						display: block;
						line-height: 21px;
						word-break: break-word;
						padding-top: 3px;
					}
					.profile-start-date, .profile-end-date{
						display: flex;
						label{
							font-weight: $text-weight-normal;
							color: $app-grey;
							width: 40px;
							&:not(:lang(en)) {
								width: unset;
							}
						}
						span{
							color: $app-grey;
							font-weight: $text-weight-medium;
							flex-grow: 1;
						}
					}
					.profile-start-date {
						padding-top: 12px;
					}
					.provide-coverage{
						margin: 8px 0 0;
					}
					.override-action{
						display: inline-block;
						position: absolute;
						right: 0;
						top: 0;
						padding: 4px;
						svg{
							width: 16px;
							height: 16px;
						}
					}
				}
				.title{
					font-weight: $text-weight-semi-bold;
				}
				.event-date-field{
					>div{
						input{
							width: 100%;
							background-color: $app-white !important;
						}
						.ember-power-select-clear-btn{
							right: 4px;
							top: 4px;
						}
					}
				}
				
				.shiftoverride-coverage-error{
					.time-control .ember-basic-dropdown .ember-power-select-trigger {
						border: 1px solid $app-danger-red;
					}
				}
				
				.shift-override-form{
					padding: 4px 8px;
					.time-control>.ember-basic-dropdown{
						width: 100px;
					}
				}
				.event-coverage-buttons{
					padding: 8px;
					text-align: right;
				}
				&.edit-true{
					flex-direction: column;
				}
			}
		}
	}
}
.shifts_display{
	#timeline{
		#timeline-info{
			padding-top: 0;
			border: none;
		}
		.shift-event{
			cursor: auto !important;
		}
	}
}