// Variables
$list-shadow-color: rgba(0, 0, 0, 0.05);

// Alert rules list
&.alerts_admin_itom_automation_settings.page-layout__page-wrapper ,&.alerts_admin_itom_alert_rules.page-layout__page-wrapper {

	:lang(sv-SE) { 
		.page-content {
			.new-alert{
				width: 152px;
			}
		} 
	}

	.page-content {
		padding-bottom: 0;
		.rule-title-section {
			@include flexbox-simple(space-between, center);
			margin-bottom: 8px;
		}

		.ams-title-with-flag {
			padding: 0 0 4px 0;
		}

		.ams-title {
			padding: 16px 0 32px 0;
		}

		.new-alert{
			@include flexbox-simple(flex-start, center);
			min-width: 136px;
			.add-rule {
				margin-left: 4px;
			}
		}

		.rule-list.dragSortList {
			min-height: 522px;
			overflow: auto;

			.dragSortItem {

				.rule-item {
					@extend .border;
					align-items: center;
					background: $app-white;
					border-radius: 4px;
					box-shadow: 0 2px 7px 0 $list-shadow-color;
					display: flex;
					justify-content: flex-start;
					margin-bottom: 16px;
					padding: 16px 16px 16px 8px;
					position: relative;
					z-index: 1;

					.handle {
						cursor: move;
						opacity: 0;
						padding-right: 8px;
						transition: 0.2s linear;

						svg {
							height: 14px;
							width: 14px;

							title { display: none; }
						}
					}

					&:hover {
						background-color: $app-sidebar-bg;

						.handle { opacity: 1; }

						.rule-name { color: $linkColor; }
					}

					a { width: calc(100% - 100px); }

					.rule-name {
						color: $app-icon-color;
						font-size: $title-small;
						font-weight: 600;
					}

					.rule-desc {
						color: $secondary-text-color-2;
						font-size: $text-normal;
						padding-top: 8px;
						width: calc(100% - 50px);

						&:empty {
							display: none;
						}
					}

					.toggle-switch {
						position: absolute;
						right: 24px;
						top: 50%;
						transform: translateY(-50%);
					}
				}

				// Hides the default drag placeholder ('[]')
				&.-placeholderAfter:before,
				&.-placeholderBefore:before {
					display: none;
				}

				// Hides the current dragged item from the list
				&.-isDragging.-isDraggingOver {
					.rule-item .hint--right {
						&:before, &:after { opacity: 0; }
					}

					.-isDragged { opacity: 0; }
				}

			}
		}
	}
}

