.integration-settings {
	.settings-helptext {
		color: $app-form-label-color;
		margin-top: 16px;
	}

	.reopen-interval {
		border: 1px solid $border-light-color;
		border-radius: 4px;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
		margin: 24px 0;
		padding: 24px 16px;

		.setting-title {
			font-weight: 500;
			margin-bottom: 0;
		}

		.setting-content {
			display: flex;
			align-items: center;
			color: $app-form-label-color;

			.ember-power-select-trigger, .ember-basic-dropdown {
				min-width: 120px;
			}

			.ember-basic-dropdown {
				padding: 0 8px;
			}
		}
	}

	.btn-primary {
		float: right;
		margin-right: 0;
	}
}
