// Step 1: Configuration
.step-1 {

	.title-2 {
		font-size: $title-small;
		font-weight: $text-weight-semi-bold;
		padding-bottom: 8px;
		padding-top: 8px;
	}

	.sub-title {
		span:first-child { width: 75%; display: inline-block; }
		span:last-child {
			float: right;
			svg {
				width: 14px;
				height: 14px;
				margin-right: 6px;
				fill: $linkColor;
			}
		}
	}

	.step-1-content-1 {
		.admin-ams-card {
			align-items: flex-start;
			border: none;
			box-shadow: none;
			cursor: default;
			height: auto;
			padding: 16px 0 0 0;

			&:hover {
				background: transparent;
				border: none;
			}

			.logo-section {
				box-shadow: 1px 4px 7px rgba(207, 215, 223, 0.4);
				height: 80px;
				width: 80px;
			}

			.content {
				align-items: flex-start;
				height: auto;
				justify-content: center;
				min-height: 80px;
				padding-bottom: 0;

				.ember-badge {
					border-radius: 38px;
					padding: 2px 8px;
				}

				.name {
					padding-bottom: 4px;
					width: 100%;
				}

				.desc {
					font-size: $text-normal;
					line-height: $baseLineHeight;
					padding-bottom: 4px;
					padding-top: 0;
					text-align: justify;
					width: 100%;
					word-break: break-word;
				}
			}
		}

		.ember-form {
			padding-bottom: 16px;

			.form-field {

				&.text-field {
					width: 520px;
				}

				&.textarea-field {
					max-width: 995px;
					width: 100%;
				}

				&.desc {
					max-width: 995px;
					width: 100%;
				}

				label {
					margin-bottom: 4px;
				}

				textarea {
					max-height: 160px;
					min-height: 100px;
					resize: vertical;
				}

				.focused {
					border-color: $inputFocusBorder;
					box-shadow: 0 0 0 1px $inputFocusBorder;
					outline: 0;
				}

				input:hover {
					border-color: $inputHoverBorder;
				}
			}

			.form-field--errors {
				margin-top: -10px !important;
			}
		}
	}

	.step-1-content-2 {
		padding-bottom: 16px;
		padding-top: 12px;
		position: relative;
		width: 100%;

		.editor-buttons {
			position: absolute;
			right: 13px;
			top: 36px;

			.btn {
				box-sizing: border-box;
				cursor: pointer;
				height: 32px;
				margin-right: 4px;
				padding: 0;
				width: 32px;
			}

			svg {
				height: 16px;
				padding-top: 4px;
				width: 16px;
			}
		}

		.invalid-format-text {
			color: $app-red;
			display: block !important;
			font-size: $text-normal;
			font-weight: $text-weight-medium;
			margin-top: 8px;
			opacity: 1;
			height: 16px;

			&.hide {
				opacity: 0;
				display: block !important;
			}
		}

		.payload-section {
			background-color: $background-color-2;
			border: 1px dashed $border-dark-color;
			border-radius: 4px;
			box-shadow: none;
			box-sizing: border-box;
			color: $disabled-text-color;
			font-size: $text-normal;
			font-weight: $text-weight-semi-bold;
			height: 248px;
			line-height: 1.5;
			margin-top: 8px;
			overflow: auto;
			padding: 16px;
			width: 100%;
			display: flex;

			&.editable {
				display: block;
				max-height: 400px;
				resize: vertical;
				background-color: $white;

				&:focus { border-color: $inputFocusBorder; }
			}

			&.error {
				border-color: $app-red-1 !important;
			}
		}
	}
}
