.integration-service-map {
	.modal-title {
		font-weight: $text-weight-semi-bold;
	}
	.description {
		margin-top: 8px;
	}
	.service-details {
		.service-map-type {
			.map-type-title {
				font-weight: $text-weight-semi-bold;
			}
			.radio-field {
				margin-top: 12px;
				@include flexbox;
				@include align-items(center);
				label {
					padding: 0;
				}
			}
		}
		.single-service {
			margin-top: 20px;
			.service-name-container {
				label {
					font-weight: $text-weight-semi-bold;
				}
				.service-name-desc {
					@include font-size(12px);
					margin-top: 4px;
					font-weight: $text-weight-normal;
				}
				.ember-basic-dropdown .ember-basic-dropdown-content {
					.ember-power-select-options {
						position: relative;
						.ember-power-select-option {
							position: initial;
							.add-service-desc {
								position: absolute;
								top: 4px;
							}
							.add-service {
								@include flexbox;
								@include align-items(center);
								svg {
									margin-right: 8px;
								}
							}
							&.ember-power-select-option--search-message, &.ember-power-select-option--no-matches-message {
								margin-top: 4px;
							}
						}
					}
					.ember-power-select-search {
						.search-container {
							top: 20px;
						}
						.ember-power-select-search-input {
							height: 36px;
						}
					}
				}
			}
			.group-info, .agent-info {
				margin-top: 16px;
				label {
					margin-bottom: 4px;
				}
				input {
					width: 300px;
				}
				.desc {
					@include font-size(12px);
					margin-top: 8px;
					font-weight: $text-weight-normal;
				}
			}
			.agent-info {
				position: relative;
				.avatar__icon {
					position: absolute;
					top: 34px;
					left: 8px;
					width: 24px;
					height: 24px;
					.avatar__initial {
						width: 24px;
						height: 24px;
					}
				}
				input.avatar-present {
					padding-left: 40px;
				}
			}
		}
	}
}
.toggle-integration-detach-modal .title {
	font-weight: $text-weight-semi-bold;
}