// Step 4: Authorization
section.step-4 {

	.sub-title {
		align-items: flex-start;
		display: flex;
		justify-content: space-between;
		margin-top: 6px;

		div:first-child { display: inline-block; }

		div.support-link-span {
			margin-left: auto;
			align-items: flex-start;
			min-width: 172px;
			text-align: right;
			.support-link {
				font-weight: $text-weight-semi-bold;
				svg {
					display: inline-block;
					position: relative;
					fill: $linkColor;
					height: 14px;
					margin-right: 6px;
					width: 14px;
				}
				span {
					vertical-align: top;
				}
			}
		}
	}

	.step-content {
		margin: 32px 0;
		.authorize-info {
			.btn-icon {
				margin-left: auto;
				margin-right: 0;
			}

			& > p {
				align-items: center;
				color: #92a2b1;
				display: flex;
				margin-bottom: 8px;
				padding: 8px 0;
				word-break: break-all;

				& > span {
					max-width: 80%;
				}
			}
		}
		.form-field {
			width: 300px;
			margin-bottom: 12px;
			label {
				color: $app-form-label-color;
				font-weight: $text-weight-semi-bold;
				@include font-size($text-normal);
				margin-bottom: 4px;
			}
		}
		.panel {
			border: 1px solid $btnDisabledBackground;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
			border-radius: 8px;
			box-sizing: border-box;
			padding: 16px;
			label {
				color: $app-desaturated-black;
				margin-bottom: 16px;
				font-weight: $text-weight-semi-bold;
			}
			.info {
				@include flexbox-simple(space-between, center);
				padding: 10px 12px;
				background: $app-sidebar-bg;
				color: $btnDisabledColor;
				border: 1px solid $primary-border;
				border-radius: $radius;
				margin-bottom: 32px;

				&.info-params {
					margin-bottom: 0px;
				}
				.info-text {
					overflow-wrap: anywhere;
				}
				.copy-btn{
					margin-right: 0px;
				}
			}
		}
	}

}

