// Alert rule details page
&.alerts_admin_itom_alert_rule.page-layout__page-wrapper {

	.hide {
		display: none;
	}

	.page-title {
		.buttons-container {
			margin-left: auto !important;
		}
	}

	.page-content {
		background: none;
		margin: 0;
		padding: 16px;

		.rule-section {
			background: $app-white;
			border-radius: 4px;
			margin-bottom: 136px;

			&.correlation-rules-section {
				padding: 15px;
			}

			.rule-title {
				border-top-left-radius: 4px;
				border-top-right-radius: 4px;
				padding: 15px 20px;

				.ams-title {
					padding-bottom: 8px;
				}

				.has-error .rule-field {

					.form-field--control {
						border-color: $app-error-state;
						box-shadow: none;
					}
					
					.form-field--hint {
						display: block;
					}
				}
				.rule-field {
					margin-top: 16px;

					label {
						font-size: $text-normal;
						color: $app-form-label-color;
						font-weight: 500;
						.star {
							color: $app-red;
						}
					}
					.form-field--control {
						width: 664px;
					}
					.form-field--hint {
						display: none;
						color: $app-red;
						font-size: $text-normal;
						margin-top: 10px;
					}

					textarea {
						max-width: 664px;
						max-height: 160px;
						min-height: 50px;
					}
				}
			}

			.edit-title {
				background: 0;
				border: 0;
				outline: 0;

				svg {
					height: 15px;
					width: 15px;
				}
			}
		}


		.incident-rule {
			padding: 15px 20px;

			.ams-sub-title {
				margin-bottom: 16px;
			}

			.rule-row {

				&:last-child {
					display: flex;
					height: 40px;
				}

				.ember-basic-dropdown {
					width: 175px;
				}

				.ember-power-select-option[aria-selected="true"] {
					display: none;
				}

				.error {
					margin-top: 8px;
				}

				.rule-container {
					width: calc(100% - 214px);
					.condition-field-name {
						width: 175px;
						margin: 0 4px;
						.ember-text-field {
							width: 100%;
							min-height: 34px;
						}
					}
					.condition-choice {
						width: 175px;
						margin: 0 4px;
					}

					.condition-value {
						width: calc(100% - 380px);
						margin: 0 4px;
					}
					.has-error {
						.ember-text-field, .fs-multiple-options-content {
							border-color: $app-error-state;
							box-shadow: none;
						}
					}
					.error {
						color: $app-red;
						font-size: $text-normal;
						margin-top: 10px;
					}
				}

				/* --- START : Incident Dependent Condition Row --- */
				.incident-rule-condition {
					width: calc(100% - 214px);
					display: flex;
					padding: 0 4px;

					> div {
						@extend .element-inline-top;
					}

					&.has-error .fs-multiple-options-content{
						border-color: $app-error-state;
						box-shadow: none;
					}

					.fs-multiple-option-input {

						&:focus {
							border-color: transparent;
							box-shadow: none;
						}
					}

					.ember-basic-dropdown {
						margin: 0 4px;
					}
					.autocomplete-field.form-field{
						width: 100%;
						.form-field--label {
							display: none;
						}
						.ember-basic-dropdown.ember-view{
							width: 100%;
							margin: 0px;
						}
					}

					.form-field {
						width: calc(100% - 190px);
						margin: 0 4px;

						.error {
							color: $app-red;
							font-size: $text-normal;
							margin-top: 10px;
						}
					}

					&.has-error .ember-power-select-multiple-trigger {
						border: 1px solid $app-error-state;
						box-shadow: none;
					}

					&.severity-condition {

						div.form-field {
							@extend .element-inline-top;
							width: auto;
						}

						div.text-content {
							@extend .element-inline;
							box-sizing: border-box;
							margin: 8px 4px 0 4px;
						}

						.ember-basic-dropdown:first-child {
							width: 296px;
						}

						input:not(.ember-power-select-trigger-multiple-input) {
							border-radius: 4px;
							height: 32px;
							margin-top: 2px;
							max-width: 48px;
							padding-left: 8px;
						}
					}
				}

				/* --- END : Incident Dependent Condition Row --- */
			}
		}

		.alert-actions {
			box-sizing: border-box;
			padding: 15px 20px;

			.action-container {
				margin-top: 16px;
				padding: 12px;
				box-sizing: border-box;
				background: $app-sidebar-bg;
				border-radius: 4px;
				border: 1px solid $app-border-primary;

				.action-choice {
					width: 196px;
					display: inline-block;
					vertical-align: top;
				}

				.action-rule-form {
					width: calc(100% - 204px);
					display: inline-block;

					.form-field {
						padding-bottom: 12px;
						display: flex;

						.form-field--label {
							min-width: 146px;
							font-weight: 500;
							font-size: $title-small;
							padding: 6px 12px 0px;
						}

						textarea.form-field--control{
							max-height: 100px;
							min-height: 50px;
						}
						.field-action-inputs {
							width: calc(100% - 146px);
						}
						&.field-action-tags {
							.ember-basic-dropdown {
								width: calc(100% - 146px);
							}
						}

						&.field-action-group {
							.ember-basic-dropdown {
								min-width: calc(100% - 640px);
								.ember-power-select-status-icon {
									margin: auto 5px auto 24px;
								}
							}
						}
					}
				}
			}
			// New UI CSS 
			.alert-rule-action {
				.action-text-option {
					margin: 8px 0px;
					display: flex;
					.action-text-option-label {
						margin: 12px 8px 12px 0px;
					}
					.action-choice {
						width: 196px;
						.ember-power-select-options {
							padding-bottom: 2px;
						}
					}
				}
				.action-incident-container {
					.ui-rule-attrs {
						width: calc(100% - 48px);


						.fields-container {
							.fields-container-key-dropdown {
								.fieldsInputEle {
									width: 140px;
									&:focus {
										border-color: transparent;
										box-shadow: none;
									}
								}
								.ember-basic-dropdown-content {
									width: 256px;
								}
							}
						}

						&.incident-field-note {
							.form-field--label {
								display: none;
							}
							.froala-editor-container {
								margin-top: 8px;
							}
						}

						.child-field {
							padding-left: 16px;
							margin-top: 8px;

							&.border-left {
								border-left: 1px solid $app-border-primary;
							}

							.form-field {
								display: flex;
								margin-top: 8px;

								.form-field--label {
									width: 112px;
									line-height: 2.25;
								}

								.ember-basic-dropdown {
									width: calc(100% - 48px);
									border: 1px solid #CFD7DF;
									background: #fff;
									border-radius: 4px;
								}
								.ember-power-select-trigger {
									display: inline-flex;
									align-items: center;
									min-height: 36px;
									border: none;
									background: none;
								}
							}
						}
					}
					.field-value-subject {
						width: calc(100% - 105px);
						.multi-value-container{
							width: 100%;

							.value-item {
								font-weight: 500;
							}
							
						}
					}
					.field-value-group {
						width: calc(100% - 130px);
					}
					.field-value-tags {
						width: calc(100% - 74px);
					}
					.field-value-status {
						width: calc(100% - 108px);
					}
					.field-value-priority {
						width: calc(100% - 116px);
					}
					.field-value-impact {
						width: calc(100% - 116px);
					}
					.field-value-urgency {
						width: calc(100% - 120px);
					}
					.field-value-department {
						width: calc(100% - 144px);
					}
					.field-value-category {
						width: calc(100% - 124px);
					}
				}
			}
		}

		.correlation-rules-section {
			opacity: 0.75;

			.correlation-rules {
				padding: 20px 0;
			}

			label { padding-bottom: 0; }
		}

	}
}
