// Alert Name and Description Modal
.ember-modal-overlay .ember-modal-dialog.createAlertRuleModal {
	border-radius: 4px;
	box-shadow: 0 2px 10px 0 $app-info-color;

	input[type='text'] { width: 100%; }

	textarea {
		max-height: 200px;
		resize: vertical;
		width: 100%;
	}

	.form-field {
		margin-bottom: 16px;
		margin-top: 10px;

		.form-field--label {
			display: block;
			padding-bottom: 8px;

			.star {
				color: $app-red;
			}
		}

		&--errors {
			color: $app-red;
			font-size: 12px;
			margin-top: 8px;
		}
	}

	.modal__footer {
		flex-direction: row-reverse;
		justify-content: flex-start;

		button:last-child {
			margin-right: 8px;
		}

		button:first-child {
			margin-right: 16px;
		}
	}
}
.alert-intelligence-modal {
	width: $modal-width !important;
	.title {
		font-weight: $text-weight-semi-bold;
	}
}
