
.business-object-details {
	padding: 20px 16px;
	.business-object-details-form {
		.business-object-content {
			.bo-title {
				display: flex;
				.business-object-details-title {
					color: $primary-text-color-1;
					font-size: 16px;
					flex: 1;
					word-break: break-word;
					padding-right: 40px;
				}
			}
			.bo-description {
				margin-top: 15px;
				width: 60%;
			}
		}
		.form-fields {
			width: 520px;
			.add-description {
				font-size: 12px;
			}
		}
	}
	.business-object-nav {
		margin-top: 30px;
		.bo-tabs {
			padding-left: 0;
			border-bottom: transparent;
			.fields-loading {
				display: flex;
				justify-content: center;
				align-items: center;
				height: 300px;
				.ember-loader {
					width: 50px;
					height: 50px;
				}
			}
			.tabs__items {
				padding-left: 0;
				border-bottom: 1px solid $dropdown-hover;
				margin-bottom: 10px;
				.tabs__item__nav {
					color: $btnPrimaryBorder;
					font-weight: 500;
					&.active {
						color: $inputFocusBorder;
					}
					&.disabled {
						color: $btnDisabledColor;
					}
				}
			}
			#fields-tab {
				padding: 20px 0;
				padding-top: 0;
			}
			#records-tab {
				padding: 20px 0;
				padding-top: 0;
			}
		}
		.obj-fields {
			.add-btn {
				display: flex;
				flex-direction: row-reverse;
				margin-bottom: 10px;
				.add-object-field {
					color: $app-black;
					font-weight: 500;
					.new-field {
						vertical-align: middle;
						margin-right: 5px;
						height: 16px;
						width: 16px;
					}
					&.disabled {
						color: $disabled-text-color;
						.new-field {
							opacity: 0.5;
						}
					}
				}
			}
			.object-fields-list {
				.field-list-wrapper{
					.list-header {
						display: flex;
						height: 40px;
						border: 1px solid $light-blue-border;
						border-radius: 4px;
						box-shadow: 0 2px 3px 0 rgba(0,0,0,0.06);
						background-color: $dropdown-option-selected;
						font-size: 12px;
						font-weight: 600;
						margin-bottom: 15px;
						.title-name {
							flex: 1;
							padding: 10px;
						}
					}
					.list-content {
						display: flex;
						border: 1px solid $inputBorder;
						min-height: 50px;
						margin-top: 8px;
						border-radius: 4px;
						box-shadow: 0 1px 6px 0 rgba(22,29,37,0.05);
						cursor: pointer;
						&:hover {
							background-color: $app-sidebar-bg;
							.field.actions .remove-field {
								display: block;
							}
						}
						.field-icon {
							height: 22px;
							width: 22px;
							margin-top: 1px;
							margin-right: 5px;
						}
						.field {
							flex: 1;
							padding: 10px;
							text-transform: capitalize;
							display: flex;
							align-items: center;
							width: 200px;
							word-break: break-word;
							&.actions {
								justify-content: flex-end;
								.remove-field {
									padding: 5px;
									border: 1px solid $inputBorder;
									border-radius: 4px;
									display: none;
									margin-right: 16px;
									svg {
										height: 18px;
										vertical-align: sub;
										fill: $inputHoverBorder;
									}
								}
							}
							.source-badge {
								color: $brand-theme-color;
								background: $dropdown-option-selected;
								border: 1px solid $light-blue-border;
								border-radius: 4px;
								padding: 3px 10px 3px 3px;
								font-size: 12px;
								font-weight: 600;
								display: flex;
								align-items: center;
								.data-source {
									padding: 3px;
									background: $white;
									border-radius: 3px;
									margin-right: 5px;
									float: left;
								}
							}
						}
						&.removed {
							border: 1px solid $app-red;
						}
					}
					.removed-label {
						font-size: 12px;
						margin-top: 10px;
						color: $app-red;
						svg {
							height: 12px;
							fill: $app-red;
						}
					}
					.no-data {
						margin-top: 20px;
						background-color: $app-sidebar-bg;
						padding: 25px 10px;
						.no-obj-field {
							margin-top: -15px;
						}
					}
				}
				
			}

		}
	}
}
