.severity-mapping {
	@include flexbox;
	margin: 0 24px;
	border: 1px dashed $inputBorder;
	border-top: none;
	padding-top: 24px;
	padding-bottom: 16px;
	border-bottom-left-radius: $radius;
	border-bottom-right-radius: $radius;
	.severity-mapping-content {
		width: 50%;
		margin: 0 24px;
		.card-row {
			@include flexbox;
			@include align-items(center);
			text-transform: capitalize;
			padding: 8px 0;
			margin: 8px 0;
			.fs-multiple-options-content input {
				text-overflow: ellipsis;
			}
			&:not(.card-row-title) {
				border-radius: 4px;
				border: 1px solid $border-light-color;
			}
			&.card-row-title {
				font-weight: $text-weight-semi-bold;
			}
			.left {
				min-width: 40%;
				margin-left: 12px;
				@include set-severity-tags();
				.mandatory-field {
					span:after {
						color: $app-error-state;
						content: "*";
						margin-left: 2px;
					}
				}
				.state-ok {
					text-transform: uppercase; 
				}
			}
			.right {
				max-width: 60%;
				.fs-multiple-options-content {
					border: none;
					&:hover {
						border: none;
					}
				}
				.error {
					margin: 8px 0 0;
					color: $app-red-1;
					@include font-size($text-normal);
				}
			}
		}
	}
	.severity-mapping-desc {
		width: calc(50% - 48px);
		margin: 8px 24px;
		padding: 24px;
		border-left: 1px solid $border-light-color;
		.note {
			margin: 0;
		}
		.heading {
			margin-top: 24px;
		}
		b {
			font-weight: bold;
		}
		ul {
			list-style: inherit;
			margin: 8px 16px;
		}
	}
}