.created-workspaces {
	.created-workspace-list {
		.workspace {
			margin: 0 11px 16px 0;
			&:hover {
				.ws-edit {
					visibility: visible !important;
				}
			}
			.ws-header-body-container {
				padding: 12px 12px 24px;
				.ws-header {
					.ws-name {
						max-width: 208px;
						flex-shrink: 0;
						margin-left: 8px;
					}
					.ws-name-and-tag {
						display: flex;
						align-items: center;
						.ws-draft-tag {
							background: $border-light-color;
							border-radius: 4px;
							margin-left: 8px;
							color: $alertCancelColor;
							font-weight: $text-weight-semi-bold;
							font-size: $text-normal;
							padding: 4px 8px;
						}
					}
					.ws-primay-selector {
						margin-left: auto;
					}
					.selector-name {
						padding: 4px;
						border-radius: 4px;
						display: flex;
						align-items: center;
						svg {
							flex-shrink: 0;
						}
						.ws-status {
							font-weight: $text-weight-semi-bold;
							font-size: $text-normal;
							line-height: 14px;
							max-width: 100px;
							margin-left: 4px;
						}
					}
					.ws-primay-selector .selector-name {
						background: $app-casablanca-lighter;
					}
				}
			}
			.ws-footer {
				border-top: 1px solid $border-light-color;
				padding: 12px;
				display: flex;
				.workspace-admins {
					font-size: $text-normal;
					color: $secondary-text-color-1;
				}
				.ws-edit {
					margin-left: auto;
					visibility: hidden;
					a {
						width: 36px;
						height: 36px;
						display: flex;
						padding: 10px;
						flex-shrink: 0;
					}
				}
			}
		}
	}

}
