// Empty state styles
.default-content {
	text-align: center;
	margin-top: 8px;

	.svg-ams-illustration {
		width: 100%;
		height: 336px;
		padding: 24px;
		margin-bottom: 24px;
		box-sizing: border-box;
	}

	.no-integration {
		width: 210px;
		height: 210px;
		padding: 24px;
		box-sizing: border-box;
	}

	.ams-title, .ams-sub-title {
		line-height: $baseLineHeight;
		text-align: center !important;
	}

	.ams-title {
		font-size: $title-small !important;
		font-weight: $text-weight-semi-bold;
		padding: 0 24px 4px 24px !important;
	}

	.ams-sub-title {
		font-size: $title-small !important;
		font-weight: $text-weight-normal;
		color: $secondary-text-color-1;
		width: 80%;
		margin: auto;
		padding-top: 8px;
	}

	button.btn-link {
		margin-top: 18px;
		font-weight: $text-weight-semi-bold;
		font-size: $title-small;
		outline: none;
	}
}