.ticket_field_suggester{
	#field-suggester-header{
		padding: 16px;
		border: 1px solid $app-border-primary;
		background-color: $app-white;
		border-radius: $inputBorderRadius;
		#header-title{
			font-weight: $text-weight-semi-bold;
			line-height: 32px;
			display: inline-block;
		}
		.toggle-switch{
			float: right;
		}
	}
	#field-suggester-sidebar{
		padding: 16px;
		box-sizing: border-box;
		width: 300px;
		.title{
			font-weight: 600;
			font-size: 17px;
			padding-bottom: 8px;
		}
	}
	#field-suggester-body{
		padding: 16px;
		border: 1px solid $app-border-primary;
		background-color: $app-white;
		border-radius: $inputBorderRadius;
		margin: 16px 0;
		#settings-title{
			font-weight: $text-weight-semi-bold;
			line-height: 32px;
		}
		#settings-fields .tfs-field{
			border: 1px solid $app-border-primary;
			border-radius: $inputBorderRadius;
			background-color: $app-white;
			margin: 8px 0;
			position: relative;
			.field-header{
				padding: 16px;
				label{
					display: inline-block;
					padding-left: 16px;
					font-weight: $text-weight-medium;
					font-size: $title-normal;
				}
				.info-icon{
					cursor: pointer;
					margin-left: 4px;
					vertical-align: middle;
					svg, path{
						fill: $app-primary
					}
				}
				.confidence_description{
					position: relative;
					left: 55px;
				}
				#field-status{
					position: absolute;
					right: 80px;
					top: 20px;
					.status-icon{
						vertical-align: middle;
						width:18px;
						height: 18px;
						&.saved{
							fill: $app-green;
							width: 20px;
							height: 20px;                        
							padding: 0 4px;
						}
					}
				}
				.field-action{
					position: absolute;
					right: 24px;
					top: 20px;
					display: none;
					.edit-button{
						width: 32px;
						height: 32px;
						border: 1px solid $app-border-primary;
						background: linear-gradient(180deg, $app-white 2.56%, $app-sidebar-bg 95.75%);
						border-radius: 4px;
						svg{
							height: 16px;
							width: 16px;
							fill: $app-grey;
						}
					}
				}
			}
			&:not(.edit-true):hover{
				background-color: $background-color-2;
				.field-action{
					display: block;
				}
			}
			&.edit-true{   
				.field-header{
					background: $app-sidebar-bg;
				}
				.field-loader{
					width: 32px;
					height: 32px;
					fill: $app-primary;
					left: 50%;
					position: relative;
				}
				.tweak-suggestions{
					padding: 16px 24px;
					.edit-field-header{
						font-size: $title-normal;
						font-weight: $text-weight-medium;
					}
					.slider-container{
						width: 80%;
						position: relative;
						margin-top: -20px;
						.confidence_slider{
							display: block;
							width: 100%;
							padding: 24px 0;
							-webkit-appearance: none;
							&::-webkit-slider-thumb{
								box-shadow: 1px 1px 1px $app-primary;
								border: 2px solid $app-primary;
								position: relative;
								height: 24px;
								width: 24px;
								border-radius: 50%;
								background: $app-white;
								cursor: pointer;
								-webkit-appearance: none;
								margin-top: -8px;
								z-index: 10;
							}
						}
						.slider-tip{
							display: inline-block;
							position: relative;
							transform: translateX(-50%);
							background: $app-white;
							padding: 4px 8px;
							top: 20px;
							font-weight: 600;
							border-radius: 4px;
							box-shadow: 0px 5px 7px rgba(0, 0, 0, 0.1);
							&::before{
								border: solid transparent;
								content: " ";
								height: 0;
								width: 0;
								position: absolute;
								pointer-events: none;
								border-top-color: $app-white;
								border-width: 6px;
								left: 50%;
								top: 25px;
							}
						}
						.slider-label{
							position: relative;
							margin-bottom: 24px;
							margin-top: -8px;
							color: $secondary-text-color-1;
							.slider-end-label{
								position: absolute;
								top: 0;
								right: 0;
							}
						}
						.selected-bar{
							position: relative;
							height: 8px;
							top: 40px;
							background: $app-primary;
							z-index: 1;
							border-radius: 4px;
						}
						.overall-bar{
							position: relative;
							width: 100%;
							top: 32px;
							height: 8px;
							border-radius: 4px;
							background: $border-light-color;
						}
					}
					.confidence-chart-container{
						max-width: 40%;
						display: inline-block;
						height: 280px;
						border: 1px solid $app-border-primary;
						border-radius: 4px;
						position: relative;
						.chart-label{
							z-index: 1;
							position: absolute;
							bottom: 50px;
							top: 195px;
							color: $secondary-text-color-1;
							&.low-label{
								left: 23%;
							}
							&.high-label{
								right: 23%;
							}
						}
						.chart-header{
							font-weight: $text-weight-medium;
							top: 20px;
							left: 50%;
							transform: translateX(-50%);
							position: relative;
							z-index: 1;
							display: inline-block;
						}
						.highcharts-wrapper{
							height: 250px;
							display: inline-block;
							overflow: hidden;
							max-width: 100%;
							.chart-container{
								position: relative;
								top: -110px;
								.highcharts-container {
									max-width: 100%;
									svg{
										width: 80%;
										height: 100%;
										box-sizing: border-box;
										position: absolute;
										left: 50%;
										transform: translateX(-50%);
									}
								}
							}
						}
					}
				}
				.edit-field-actions{
					height: 50px;
					padding: 8px 16px;
					background: $app-sidebar-bg;
					button{
						float: right;
					}

				}
			}
		}
		#settings-fields .tfs-field.loading-true, #settings-fields .tfs-field.loading-true ~ .tfs-field{
			.toggle-switch {
				cursor: not-allowed;
				.slider{
					pointer-events: none;
				}
			}
		}
	}

	#tfs_eligibility_body{
		width: calc(100% - 40px);
		background: $app-white;
		margin: 20px;
		padding-top: 40px;
		padding-bottom: 100px;
		display: flex;
		align-items: center;
		flex-direction: column;
		position: relative;
		> img {
			margin-bottom: 30px;
		}
	
		> p{
			color: $app-desaturated-black;
			font-weight: $text-weight-semi-bold;
			font-size: 17px;
			margin-bottom: 0;
			text-align: center;
			line-height: 1.5;
		}
	
		p:last-child{
			color: $secondary-text-color-2;
			font-size: $title-small;
			text-align: center;
			line-height: 1.5;
		}
	}
}
