&.alerts_admin_itom_alert_integration {

	&.page-layout__page-wrapper {

		.page-layout-content {
			background-color: $white;
		}

		.page-sidebar {
			background-color: $white;
		}

		.page-content {
			margin: 0;
			border-radius: 0;
			padding: 24px 56px;
			min-height: calc(100vh - 60px);
		}
	}

	.integrations-list {
		@import "description-container";
	}
	.default-content {
		padding-top: 32px;

		.ams-sub-title { width: 500px; }

		.btn-link {
			@include flexbox;
			@include justify-content(center);
			@include align-items(center);
			width: 152px;
			margin: 16px auto;

			svg.add {
				margin-right: 4px;
			}
		}
	}

	.integration-list-header {
		background: $background-color;
		font-weight: $text-weight-semi-bold;
		border-radius: 4px;
		padding: 12px;
	}

	@import "integration-list-items";
}