//Variables
$ams-light-black: rgba(0, 0, 0, 0.5);

@mixin admin-alert-base() {
	/* --- Base Utilities for the page --- */

	.ams-title {
		@extend .title;
		font-weight: 600;
		font-size: 16px;
	}

	.ams-sub-title {
		font-size: 12px;
		color: $ams-light-black;
		padding-top: 8px;
	}

	.ams-soft-hide {
		opacity: 0;
		height: 0;
		border: 0;
	}

	.help-text {
		margin: 10px 0;
	}
}

// --> end of @mixin admin-alert-base

@mixin flex-hv-center() {
	display: flex;
	justify-content: center;
	align-items: center;
}

// Alert severity states
$severities: 'ok', 'warning', 'error', 'critical';

// Setting severity color tags : <state-color> <state-text>
@mixin set-severity-tags() {
	@each $severity in $severities {
		.state-#{$severity} .tag {
			width: 8px;
			height: 8px;
			display: inline-block;
			border-radius: 2px;
			margin-right: 5px;
			@extend %bg-severity-#{$severity};
		}
	}
}

// Used in alert profiles and integrations lists
.admin-ams-card {
	background: $app-white;
	border: 1px solid $border-light-color;
	border-radius: 4px;
	box-shadow: 0 1px 6px rgba(22, 29, 37, 0.05);
	box-sizing: border-box;
	cursor: pointer;
	display: flex;
	height: 104px;
	margin-bottom: 16px;
	padding: 16px;

	&:hover {
		background: $background-color-2;
		border: 1px solid $border-light-blue;
		border-radius: 4px;
		box-shadow: 0 1px 6px rgba(22, 29, 37, 0.05);
		box-sizing: border-box;
	}

	.logo-section {
		background: $app-white;
		border: 0.5px solid $border-light-color;
		border-radius: 4px;
		box-sizing: border-box;
		height: 72px;
		margin-right: 16px;
		text-align: center;
		width: 72px;
		@include flex-hv-center();

		img { height: 60px; width: 60px; }

		&.new-relic-apm {
			img {
				height: 50px;
				padding-top: 12px;
				width: 50px;
			}
		}

		&.new-relic-synthetics {
			img {
				height: 70px;
				width: 70px;
			}
		}

		&.custom-integration, &.email {
			.logo-wrapper {
				background: $app-grey;
				border-radius: 50%;
				height: 40px;
				width: 40px;
				@include flex-hv-center();

				img { height: 24px; width: 24px; }
			}
		}

		&.microsoft-scom {
			img { height: 64px; width: 64px; }
		}
		&.logic-monitor {
			img { height: 76px; width: 76px; }
		}
	}

	.content {
		align-items: flex-start;
		color: $primary-text-color-1;
		display: flex;
		flex-direction: column;
		height: 72px;
		justify-content: center;
		width: calc(100% - 88px);

		.name, .desc { width: calc(100% - 88px); }

		.name {
			font-size: $title-small;
			font-weight: $text-weight-semi-bold;
		}

		.desc {
			font-size: $title-small;
			font-weight: $text-weight-normal;
			padding-top: 4px;
		}

		.meta {
			color: $secondary-text-color-1;
			font-size: $text-normal;
			font-weight: normal;
			padding-top: 8px;
		}
	}
}

&[class^='alerts_admin_itom_'].page-layout__page-wrapper {

	@include admin-alert-base;
	@import "alert-integrations/default-integrations-list";

	.ams-title-large{
		@extend .ams-title;
		@include font-size($text-large);
	}

	.back-btn {
		box-shadow: 0px 2px 10px $box-shadow-color-2;
		border: none;
		border-radius: 6px;
		background: $app-white;
	}

	.page-title {
		box-sizing: border-box;
		display: flex;
		min-height: 32px;

		svg {
			height: 20px;
			width: 20px;
		}

		.right {
			margin-left: auto !important;
			margin-right: -8px;
		}
	}

	.page-content {
		background: $app-white;
		border-radius: 4px;
		flex: 1;
		margin: 16px;
		overflow: hidden;
		padding: 16px;
		width: calc(100% - 296px);
	}

	.page-sidebar {
		box-sizing: border-box;
		padding: 16px;

		.content {
			margin-bottom: 24px;

			a { font-size: $title-small; font-weight: $text-weight-semi-bold; }

			.title {
				color: $primary-text-color-1;
				font-size: $title-normal;
				font-weight: bold;
				line-height: 1.5;
			}

			.help-text {
				color: $primary-text-color-1;
				font-size: $title-small;
				line-height: 1.5;
				margin-bottom: 0;
			}
		}
	}
}
