&.alerts_admin_itom_alert_integrations_alert_payload.page-layout__page-wrapper {
	@import "./manage-integrations/edit-integration";
	@import "sticky-footer";

	.edit-container{
		padding: 0 0 8px 24px;

		.edit-form-container{
			margin-top: 0;
		}
	}

	.page-content {
		margin: 0;
		border-radius: 0;
		padding: 24px 0;
		min-height: calc(100vh - 60px);
	}

	.alerts_admin_itom_integrations_payload {
		padding: 0 64px;

		.title-content {
			@include flexbox;
			@include align-items(center);
			width: 100%;
			margin-bottom: 24px;

			span {
				color:$app-info-color;
			}

			.back-btn {
				margin-right: 32px;
			}
		}

		.payload-section {
			@include font-size($baseFontSize);
			background-color: $background-color-2;
			border: 1px dashed $border-dark-color;
			font-weight: $text-weight-normal;
			width: calc(100% - 128px);
			margin: 0px 64px;
			padding: 12px;
			border-radius: $radius;
			box-shadow: none;
			box-sizing: border-box;
			overflow: auto;
		}
	}

	.admin_itom_integrations_custom_payload {
		padding:20px 48px 88px;

		&.is-edit {
			padding-top: 0px;
			.content-section {
				margin: 12px 64px 24px;
			}
		}

		.header-section {
			@include flexbox;
			@include align-items(center);

			.back-btn {
				margin-right: 32px;
			}

			.ams-title-large {
				span {
					color:$app-info-color;
				}
			}
		}

		.content-section {
			margin-bottom: 24px; 

			.payload-edit-section {
				@include font-size($baseFontSize);
				background-color: $white;
				border: 1px dashed $border-dark-color;
				font-weight: $text-weight-normal;
				width: 100%;
				height: 284px;
				max-height: 400px;
				padding: 12px;
				border-radius: $radius;
				box-shadow: none;
				overflow: auto;
				resize: vertical;

				&:focus {
					border-color: $inputFocusBorder;
				}

				&.error {
					border-color: $app-red-1 !important;
				}
			}

			.info-section {
				background-color: $section-background-blue;
				border: 1px solid $app-primary;
				margin-top: 8px;
				margin-bottom: 16px;
				border-radius: $radius;
				padding: 16px 20px;

				ul {
					list-style: disc;
					margin-left: 14px;
				}

				a {
					font-weight: $text-weight-semi-bold;

					span {
						margin-left: 4px;

						svg {
							width: 10px;
							height: 10px;
						}
					}
				}
			}

			.invalid-format-text {
				@include font-size($text-normal);
				color: $app-red;
				font-weight: $text-weight-medium;
				margin-top: 8px;
				height: 16px;
			}
		}
	}
}
