.marketplace {
	.page-layout-content {
		height: calc(100% - 105px);
	}

	.marketplace-content,
	.marketplace-gallery-container {
		height: calc(100vh - 70px);
	}
	.marketplace-gallery-container {
		padding: 0px;
	}
	.marketplace-gallery {
		height: 100%;
		.fs-animation {
			padding: 0px;
		}
		.hide {
			display: none;
		}
	}
	.marketplace-gallery-content {
		height: 100%;
		iframe {
			height: 100%;
			width: 100%;
		}
	}
	.marketplace-page-content {
		margin: 0px;
		max-height: 100%;
		width: 100%;
	}

	#gallery-frame {
		#header-top-bar,
		.top_nav_bg {
			display: none !important;
		}
	}
}

.enable-fp-nav-bar {
	.marketplace-content,
	.marketplace-gallery-container {
		height: calc(100vh - 110px) !important;
	}
}
