.cloud-management-container {
	.cm-card {
		background: $white;
		border-radius: 4px;
		box-shadow: 0 1px 4px $box-shadow-color-1;
		&:not(:first-child) {
			margin-top: 8px;
		}
		.cm-card-header {
			padding: 16px;
			color: $primary-text-color-1;
			border-bottom: 1px solid $app-border-primary;
			h4 {
				@include font-size($title-normal);
				font-weight: $text-weight-bold;
				padding-bottom: 4px;
			}
			.cm-subtitle {
				font-weight: $text-weight-normal;
				font-size: $title-small;
			}
		}
		.cm-section-wrapper {
			&.cm-intro {
				padding: 24px 32px 16px 16px;
			}
			&.cm-integration {
				padding: 14px 32px 16px 16px;
			}
			.cm-gallery-view-wrapper {
				color: $secondary-text-color-1;
				.cm-gallery-view-header {
					.cm-gallery-title {
						@include font-size($title-normal);
						font-weight: $text-weight-semi-bold;
						padding-bottom: 4px;
					}
					.cm-gallery-subtitle {
						font-size: $text-normal;
						font-weight: $text-weight-normal;
					}
				}
				.cm-gallery-app-list {
					@include flexbox;
					@include justify-content("flex-start");
					@include flex-direction("column");
					padding-bottom: 32px;
					flex-wrap: wrap;
					.loader-container {
						padding-top: 16px;
					}
					.cm-gallery-item {
						@include flexbox;
						@include border-style;
						background: $white;
						height: 90px;
						padding: 8px;
						flex-shrink: 0;
						margin: 16px 16px 0 0;
						box-shadow: 0 1px 4px $box-shadow-color-1, 0 1px 3px $box-shadow-color-1;
						cursor: pointer;
						@media (min-width: 1024px) {
							width: calc(25% - 16px);
						}
						@media (min-width: 768px) and (max-width: 1024px) {
							width: calc(33% - 16px);
						}
						@media (max-width: 768px) and (min-width: 425px) {
							width: calc(50% - 16px);
						}
						@media (max-width: 425px) {
							width: calc(100% - 16px);
						}
						&:hover {
							background: $background-color-2;
						}
						img {
							width: 72px;
							height: 72px;
							padding: 8px;
							margin-right: 8px;
							@include border-style;
						}
						.app-item-wrapper {
							width: 100%;
							.app-item-title {
								color: $primary-text-color-1;
								@include font-size($title-small);
								font-weight: $text-weight-medium;
								padding: 4px 0;
								span {
									text-transform: capitalize;
									width: calc(100% - 36px);
								}
								svg {
									float: right;
									width: 20px;
									height: 20px;
									margin: 2px 8px;
									&.disabled path {
										fill: $btnDisabledColor;
									}
								}
							}
							.app-item-desc {
								@include font-size($text-normal);
								font-weight: $text-weight-normal;
								height: 40px;
								overflow: hidden;
							}
						}
					}
				}
			}
		}
	}
}
