&.alerts_admin_itom_integrations_advanced_settings.page-layout__page-wrapper {
	@import "sticky-footer";

	.page-content {
		margin: 0;
		border-radius: 0;
		min-height: calc(100vh - 60px);
		padding: 24px 0;
	}

	.alert-integration-adv-settings {
		@include flexbox;
		padding: 0px 64px;

		.settings-content {
			width: 100%;
			margin-top: 8px;

			.btn {
				float: right;
				min-width: 88px;

				&.remove-mrg {
					margin-right: 0px;
				}
			}
		}

		.back-btn {
			box-shadow: 0px 2px 10px $box-shadow-color-2;
			border: none;
			border-radius: 6px;
			background: $app-white;
			margin-right: 32px;

			svg {
				height: 16px;
				width: 16px;
			}
		}

		.settings-title {
			font-size: $title-large;
			font-weight: $text-weight-semi-bold;
			margin-bottom: 8px;

			span {
				color:$app-info-color;
			}
		}

		.settings-subtitle {
			font-size: $text-normal;
			font-weight: $text-weight-normal;
			color: $secondary-text-color-1;
			margin-bottom: 16px;
		}

		.opt {
			border: 1px solid $border-light-color;
			padding: 16px 24px;
			border-radius: $radius;
			margin-bottom: 16px;

			&.checkbox {
				@include flexbox;
				@include align-items(center);
				padding: 15px 16px;
				margin-bottom: 24px;

				.ember-checkbox {
					margin-top: 0;
				}

				.checkbox-text {
					display: inline-block;
					padding: 0;
				}
			}
		}

		.opt-title {
			color: $secondary-text-color-1;
			font-weight: $text-weight-semi-bold;
		}

		.opt-content {
			@include flexbox;
			@include align-items(center);
			color: $app-form-label-color;

			.ember-power-select-trigger,
			.ember-basic-dropdown {
				min-width: 120px;
			}

			.ember-basic-dropdown {
				padding: 0 8px;
			}
		}
	}
}
