.recommended-feature-details {
	padding: 32px;
	overflow-y: auto;
	.feature-header {
		display: flex;
		.feature-name {
			font-weight: $text-weight-semi-bold;
			font-size: $title-extra-large;
			line-height: 32px;
		}
		.feature-cta {
			margin-left: auto;
			a {
				margin: 0;
			}
		}
	}
	.feature-info {
		width: 444px;
		margin: 6px 0 20px;
		font-size: $text-normal;
		line-height: 18px;
		color: $secondary-text-color-1;
	}
	.feature-benefits {
		li {
			display: flex;
			margin-top: 12px;
			.checkmark {
				fill: $app-success-state;
				width: 14px;
				height: 14px;
				margin-top: 2px;
				vertical-align: bottom;
			}
			span {
				margin-left: 8px;
				color: $app-black;
			}
		}
	}
	.feature-usage {
		display: flex;
		border: 1px solid $border-dark-color;
		border-radius: 8px;
		margin-top: 26px;
		padding: 16px;
		.magic-wand {
			margin-top: 3px;
		}
		.usage-details {
			margin-left: 8px;
			.usage-info {
				margin: 0;
				font-weight: $text-weight-medium;
				line-height: 24px;
				span {
					font-weight: $text-weight-bold;
				}
			}
			.usage-bar-title {
				font-weight: $text-weight-semi-bold;
				font-size: $text-normal;
				color: $secondary-text-color-1;
				margin-top: 18px;
			}
			.usage-bar-container {
				position: relative;
				margin-top: 48px;
				.usage-bar {
					display: flex;
					.bar-1,.bar-2 {
						width: 110px;
						height: 7px;
					}
					.bar-1 {
						background-color: $hummingBirdGreenBorder;
						border-radius: 4px 0 0 4px;
					}
					.bar-2 {
						background-color: $app-green;
						border-radius: 0 4px 4px 0;
					}
				}
				.percentage-numbers {
					color: $secondary-text-color-1;
					font-weight: $text-weight-semi-bold;
					font-size: $text-normal;
					margin-top: 10px;
					span:nth-child(2) {
						margin-left: 93px;
					}
					span:nth-child(3) {
						margin-left: 90px;
					}
				}
				#percentage-pointer {
					position: absolute;
					width: 14px;
					height: 14px;
					padding: 2px;
					border-radius: 50%;
					top: -4px;
					left: 0px;
					background: $white;
					box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.18);
					.inner-pointer {
						width: 10px;
						height: 10px;
						margin: 0;
						background-color: $linkColor;
						border-radius: 50%;
					}
				}
				#used-percentage {
					position: absolute;
					top: -40px;
					.circle-container {
						width: 33px;
						height: 24px;
						background: $linkColor;
						border-radius: 64px;
						.percentage {
							font-size: $text-normal;
							font-weight: $text-weight-semi-bold;
							padding-top: 2px;
							text-align: center;
							color: $white;
						}
					}
					.circle-arrow-down {
						margin-left: 11px;
						margin-top: -1px;
						width: 0; 
						height: 0; 
						border-left: 6px solid transparent;
						border-right: 6px solid transparent;
						border-top: 7px solid $linkColor;
					}
				}
			}
		}
	}
	.popular-usecases {
		margin-top: 32px;
		.title {
			font-weight: $text-weight-semi-bold;
			font-size: $text-large;
			margin-bottom: 16px;
		}
		.tabs-container {
			display: flex;
			.tab {
				position: relative;
				margin: 0 12px 12px 0;
				.tab-info {
					min-width: 80px;
					padding: 12px;
					background-color: $app-white;
					border: 1px solid $border-dark-color;
					border-radius: 4px;
					cursor: pointer;
					font-weight: $text-weight-semi-bold;
					font-family: $baseFontFamily;
					color: $primary-text-color-1;
				}
				.tab-arrow {
					display: none;
					width: 0;
					height: 0;
					border-left: 8px solid transparent;
					border-right: 8px solid transparent;
					border-top: 9px solid $linkColor;
					margin: -1px auto 0;
				}
				&.active {
					.tab-info {
						border: 1px solid $linkColor;
					}
					.tab-arrow {
						display: block;
					}
				}
			}
		}
		.tabs-banner {
			li {
				display: none;
				position: relative;
				&.active {
					display: block;
				}
				.use-case-info {
					width: 690px;
					position: absolute;
					top: 28px;
					left: 33px;
					text-align: center;
					font-weight: $text-weight-semi-bold;
					line-height: 18px;
					color: $app-white;
				}
			}
		}
	}
}