//Banner for next step.
.alert-banner {
	margin: 8px 16px 16px;
	box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.06);
	border: 1px solid $border-light-color;
	border-radius: 8px;
	width: calc(100% - 32px);
	@include flexbox;
	@include align-items(center);
	.next-steps-text{
		@include font-size(18px);
		color: $app-white;
		opacity: 1;
		z-index: 10;
		position: absolute;
		left: 136px;
		font-weight: $text-weight-semi-bold;
		margin-top: 34px;
	}
	.next-step-icon {
		height: 100px;
		margin-top: 4px;
		border-radius: 8px;
	}
	.next-step-content {
		margin-right: 8px;
		.heading {
			font-weight: $text-weight-semi-bold;
			@include font-size(16px);
			margin: 4px 0;
			color: $app-warning-state;
		}
		.content {
			@include font-size(14px);
		}
	}
	.next-btn {
		padding: 16px 24px;
		font-weight: $text-weight-semi-bold;
		border-left: 1px solid $border-light-color;
	}		
}