.edit-container{
	border-bottom: $regular-border;
	@include flexbox;
	@include align-items(flex-start);
	padding-top: 8px;

	.edit-form-container{
		margin: 16px 24px 24px;
		label {
			margin: 4px 0;
			@include font-size($text-normal);
			font-weight: $text-weight-semi-bold;
			padding-bottom: 0;
		}
		.description-container {
			.textarea-field {
				margin-top: 8px;
				textarea {
					height: 120px;
					width: 760px;
				}
			}
			.description-errors {
				margin: 8px 0 0;
			}
		}
		.name-container {
			width: 40%;
			input {
				width: 240px;
			}
		}
		
	}
	.setting-container{
		@include flexbox();
		@include align-items(center);
		margin-top: 32px;
		width: calc(60% - 36px);
		button{
			@include font-size($title-small);
			padding: 4px 12px;

			svg {
				fill: $svg-icon-color;
				width: 16px;
				height: 18px;
			}

			.btn-text {
				margin-left: 4px;
			}
		}

		div.alert-count-info {
			margin-left: auto;
		}

		.btn-icon {
			margin-left: 8px;
		}

		.alert-font{
			color: $app-primary;
		}
	}
}