.line-height-20 {
	line-height: 20px;
}

.create-shift-icon {
	margin-left: auto;
	@include flexbox();
	align-items: center;
	border: none;
	svg {
		width: 14px;
		height: 14px;
		padding: 3px;
		margin-right: 4px;
		box-sizing: unset;
	}
	&:active {
		border: none;
		box-shadow: none;
	}
}

.on-call-schedule-container {
	.page-layout-content {
		min-height: 100vh;
	}
	.tabs{
		border-bottom: none;
	}
	.bullet-list {
		@include font-size(14px);
		color: $secondary-text-color-1;
		padding-left: 16px;
		li {
			padding: 2px 0px;
			margin-bottom: 2px;

			&::before {
				content: "\2022"; // Circle bullet character
				color: $secondary-text-color-2;
				display: inline-block;
				width: 16px;
				font-size: 16px;
				line-height: 16px;
				font-weight: $text-weight-bold;
				margin-left: -16px
			}
		}
	}
	.schedule_display {
		padding: 0 0 24px;
		margin-left: 32px;
		word-wrap: break-word;
		.agent_group {
			@extend .line-height-20;
			padding-bottom: 2px;
		}

		.schedule_name_title {
			font-size: 16px;
			width: 90%;
			font-weight: $text-weight-semi-bold;
			@extend .line-height-20;
			display: inline-block;
		}

		.schedule_name_description {
			padding-top: 4px;
			line-height: 16px;
		}
		.agent_group_label{
			margin-top: 16px;
		}
		.schedule_name_groupname {
			@include font-size(12px);
			font-weight: $text-weight-semi-bold;
			margin: 8px 0;			
			color: $secondary-text-color-1;
		}
		.schedule-note {
			@include font-size(14px);
			color: $secondary-text-color-1;
		}
	}
	#shifts{
		padding: 0;
		.shifts_display {
			padding: 24px 0 32px 32px;

			.shift_name_title {
				font-size: 16px;
				font-weight: $text-weight-semi-bold;
				padding-bottom: 12px;
				@extend .line-height-20;
				display: inline-block;
			}
			.shift-section #timeline .layer-schedule {
				overflow-x: clip;
			}
		}
	}

	.pull-right {
		float: right;
		padding-bottom: 12px;
	}

	.border-red {
		border-color: $app-red !important;
	}
	.schedule-view-tabs {
		padding-left: 32px;
		border-bottom: 1px solid $border-dark-color;
		.tabs__item__nav{
			font-weight: $text-weight-semi-bold;
			padding: 0;
			margin-right: 16px;
			&:focus{
				border-color: $inputFocusBorder;
				box-shadow: 0 0 0 1px $inputFocusBorder;
				outline: 0;
			}
		}
		#notification-rules {
			padding-bottom: 80px;
		}
	}
	.ocs-notify-rules {
		padding: 12px 32px 32px 16px;
		max-width: 1080px;
		.notify-desc {
			color: $secondary-text-color-1;
			@include font-size(14px);
			max-width: 850px;
		}
		h3 {
			font-weight: $text-weight-bold;
			color: $secondary-text-color-1;
			margin: 30px 0 12px 0;
		}
		.tabs__items {
			padding-left: 0px;

			.tabs__item {
				font-weight: $text-weight-semi-bold;
				@include font-size(14px);
				color: $secondary-text-color-1;
				border: 1.5px solid $border-light-color;
				box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
				border-radius: 8px;
				padding: 8px;
				margin-bottom: 18px;
				min-width: 140px;
				overflow: unset; 
				&:hover::after {
					background: none;
				}
				&:focus{
					outline: none;
					background: $btnBackground;
					border-color: $inputFocusBorder;
					box-shadow: 0 0 0 1px $inputFocusBorder;
				}
				&.active {
					border: 2px solid $inputFocusBorder;
					position:relative;
					svg {
						fill: unset;
					}
					&::after {
						background: none;
						position: absolute;
						width: 0;
						height: 0;
						content: '';
						top: 100%;
						left: 0;
						right: 0;
						margin: 0 auto;
						border-left: 10px solid transparent;
						border-right: 10px solid transparent;
						border-top: 10px solid $sliderTrackFill;
					}
				}
				.tab-title--tab-icon {
					@include flexbox-simple(flex-start, center);
					.app-icon {
						margin-right: 8px;
						width: 20px;
						height: 20px;
					}
				}
			}
		}
		.tabs__content {
			border: 1px solid $inputFocusBorder;
			box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.06);
			border-radius: 8px;

			// TODO: this can be removed after the common alert component is available.
			#notify-channel-phone-sms {
				.ember-alert {
					height: 60px;
					padding: 10px;
					@include align-items(flex-start);

					svg {
						min-width: 16px !important;
						fill: gray !important;
					}

					p{
						@include font-size(14px)
					}

				}
			}

		}
		.notify-channel-info {
			b, .channel-link {
				font-weight: $text-weight-semi-bold;
			}
			.channel-link svg {
				fill: $linkColor;
				width: 10px;
				height: 10px;
				margin: 0 4px;
			}
		}
		.notify-priority-rule {
			.notify-priority-tabs{
				.priority {
					padding-left: 28px;
					&::before {
						background: $muted-secondary;
						border-radius: 4px;
						position: absolute;
						width: 12px;
						height: 12px;
						content: '';
						top: 50%;
						bottom: 50%;
						left: 8px;
						margin: auto 0;
					}
					&.priority-0::before {
						background: $priorityUrgentColor;
					}
					&.priority-1::before {
						background: $priorityHighColor;
					}
					&.priority-2::before{
						background: $priorityMediumColor;
					}
					&.priority-3::before {
						background: $priorityLowColor;
					}
					.tab-title{
						font-weight: $text-weight-semi-bold;
					}
				}
			}
			.actions {
				border-bottom: 1px solid $border-light-color;
				padding-bottom: 22px;
				margin-bottom: 22px;
			
				.action-con {
					@include flexbox();
					@include justify-content(space-between);
					.desc-text {
						@include font-size(14px);
						color: $secondary-text-color-1;
						width: 600px;
					
						b {
							font-weight: $text-weight-semi-bold;
							margin: 0px 4px;
						}
					}
			
					.action-btns {
						@include flexbox-simple(flex-end, center);
						.btn {
							min-width: 80px;
			
							&.hasNoChanges {
								visibility: hidden;
							}
			
							&.btn-primary.disabled {
								background: linear-gradient(180deg, $btnDisabledColor 4.39%, $disabled-button-border 94.46%);
								border: 1px solid $disabled-button-border;
								color: $white !important;
								opacity: 0.75;
							}
						}
					}
				}
			}
			.toggle-escalation {
				flex-grow: 4;
				@include flexbox();
			
				.desc-text {
					margin-left: 8px;
				}
			}
			
			.escalation-time-alert {
				margin-top: 16px;
				margin-bottom: 4px;
				height: 36px;
				width: 576px;
			
				.alert-icon {
					height: 20px;
			
					svg {
						margin-top: 0px;
						fill: $btnDangerBorder;
					}
				}
			}			
			
			.escalation-selected-time {
				.ember-basic-dropdown {
					min-width: 32px;
				}
				.ember-power-select-dropdown {
					width: 100px;
				}
				
				.ember-power-select-selected-item { 
					margin-left: 8px;
					margin-top: 2px;
				}
			}

			.default-action {
				@include flexbox-simple(flex-start, center);
				margin-bottom: 8px;
				@include font-size(12px);
				b {
					font-weight: $text-weight-semi-bold;
					margin: 0px 4px;
				}
			}
			.rule-container {
				margin: 0px 0px 16px 26px;
				.rule-row {
					@include flexbox-simple(flex-start, center);
					border: none;
					background: none;
					padding-top: 4px;
					@include font-size(12px);
					&:last-child {
						padding-top: 8px;
						@include font-size(14px);
					}
				}
			}
			.inline-ui-comp {
				margin: 0px 6px;
				@include font-size(12px);
				font-weight: $text-weight-semi-bold;
				padding-right: 4px;
				width: 40px;
				&.no-spin {
					-moz-appearance: textfield;
					&::-webkit-outer-spin-button,
					&::-webkit-inner-spin-button {
						-webkit-appearance: none;
						margin: 0;
					}
				}
			}
			.ember-basic-dropdown {
				margin: 0px 4px;
				min-width: 320px;
				.ember-power-select-multiple-option {
					font-size: 12px;
					font-weight: $text-weight-semi-bold;
				}
			}
			.help-text {
				@include font-size(12px);
				color: $secondary-text-color-1;
				margin-bottom: 12px;
			}

			.error-action {
				margin-bottom: 32px;
				&.default-action {
					margin-bottom: 40px;
					&.exceedMax {
						.interval-error .error-text {
							&.exceedMax {
								margin-left: -148px;
							}
						}
						.channel-error .error-text {
							margin-left: 80px;
						}
					}
				}
				.interval-error {
					.inline-ui-comp {
						border: 2px solid $app-error-state;
						box-shadow: inset 0px 1px 2px rgba(24, 50, 71, 0.05);
					}
					.error-text {
						&.exceedMax {
							margin-left: -220px;
						}
						&.invalid {
							margin-left: -40px;
						}
					}
				}
				.channel-error .ember-power-select-trigger {
					border: 2px solid $app-error-state !important;
					box-shadow: inset 0px 1px 2px rgba(24, 50, 71, 0.05);
					border-radius: 4px;
				}
				.error-text {
					@include font-size(14px);
					color: $app-error-state;
					margin-top: 8px;
					padding-left: 4px;
					position: absolute;
				}
			}

			.disable-escalation-fields {
				.default-action, .ember-power-select-multiple-option, .rule-row, .inline-ui-comp{
					color: $disabled-button-border;
				}
				.ember-power-select-multiple-options{
					padding-top: 2px;
					.ember-power-select-multiple-option {
						padding: 2px 4px;
					}
				}	
				.ember-power-select-trigger-multiple-input {
					display: none;
				}
			}
		}

		&.ocs-view-only {
			.notify-channel {
				.ember-power-select-multiple-trigger {
					border: none;
					background: none;
					.ember-power-select-multiple-option {
						line-height: 24px;
						padding: 2px 8px;
					}
					.ember-power-select-trigger-multiple-input {
						display: none;
					}
				}
			}
		}
	}
}

.ember-modal-overlay .ember-modal-dialog.ember-modal-confirm.notifyRuleDiscardModal {
	width: 480PX;
	.ember-modal-body {
		margin-bottom: 32px;
	}
	.ember-modal-footer .btn {
		min-width: 80px;
	}
}

.delete_schedule_button {
	border: 1px solid $btnDangerBorder;
	width: 80px;
	background: $btnDangerBackground;
	&:hover {
		background: $btnDangerBorder;
		border-color: $btnDangerBorder !important;
	}
}

.delete_cancel_schedule_button {
	width: 80px;
}
 
.delete_schedule_modal_container {
	width: 512px;
	top: 30%;
	left: 50%;
	transform: translate(-50%, -50%);

	.title {
		font-size: 18px;
		font-weight: 600;
	}

	.delete_schedule_text {
		@extend .line-height-20;
	}

	.delete_schedule_gap {
		height: 7px;
	}
	.ember-modal-close {
		display: none;
	}
}
