.description-container {
	@include flexbox;
	@include align-items(center);
	margin-top: 16px;
	.add-integration-btn {
		@include flexbox;
		@include align-items(center);
		color: $app-grey;
		margin-left: auto;
		font-weight: $text-weight-semi-bold;
		outline: none;
		margin-right: 0;
		border: 0;

		&:active {
			border: 0;
			outline: none;
			box-shadow: none;
		}

		svg.add {
			height: 14px;
			padding-right: 4px;
			vertical-align: middle;
			width: 14px;
			margin-right: 4px;
			box-sizing: unset;
		}

		&.disabled {
			color: $disabled-text-color;

			svg.add {
				.add-outer {
					fill: $white;
					stroke: $disabled-text-color;
				}

				.add-sign {
					fill: $disabled-text-color;
				}
			}
		}
	}
}