.payload-mapping {
	margin: 24px 24px 0;
	.payload-mapping-title {
		font-weight: $text-weight-semi-bold;
		@include font-size($title-normal);
	}
	.payload-mapping-desc {
		width: 800px;
		margin: 16px 0;
	}
	.card-row {
		@include flexbox;
		@include align-items(center);
		padding: 8px 0;
		margin: 8px 0;
		.fs-multiple-options-content input {
			text-overflow: ellipsis;
		}
		&:not(.card-row-title) {
			border-radius: 4px;
			border: 1px solid $border-light-color;
		}
		&.card-row-title {
			font-weight: $text-weight-semi-bold;
			text-transform: capitalize;

			.center {
				text-align: left;
			}
		}

		&:last-child {
			margin-top: 8px;
			margin-bottom: 0;
		}
		.left {
			min-width: calc(20% - 16px);
			margin: 8px 16px;
			.required {
				color: $light-red;
				@include font-size($title-normal);
			}
		}
		.center {
			position: relative;
			min-width: calc(33% - 16px);
			margin: 8px;
			.fs-multiple-options-content {
				.fs-multiple-option {
					margin: 2px;

					.fs-multiple-option-text {
						text-align: left;
					}
				}

				&.allow-multiple {
					padding-right: 36px;
				}
			}
			.payload-label-selector {
				width: 332px;
				position: relative;
				&:not(.active) {
					.ember-modal-dialog.field-labels-selector {
						display: none;
					}
				}
				.add-btn {
					position: absolute;
					border: 1px solid $primary-border;
					border-radius: 0 2px 2px 0;
					color: $linkColor;
					cursor: pointer;
					@include font-size($title-small);
					height: 100%;
					line-height: 1;
					outline: 0;
					right: 0;
					top: 0;
					padding: 0;
					background-color: $section-background-blue;
					&:hover {
						opacity: 1;
						background-color: $app-primary;
						
						svg {
							path {
								&:first-child {
									fill: $app-primary;
									stroke: $app-primary;
								}
								&:last-child {
									fill: $app-white;
								}
							}
						}
					}
					&.disabled {
						background: $border-light-color;
						color: $btnDisabledColor;
						cursor: not-allowed;
						pointer-events: auto;

						svg {
							path {
								&:first-child {
									fill: $btnDisabledBackground;
									stroke: $btnDisabledBackground;
								}
								&:last-child {
									fill: $disabled-text-color;
								}
							}
						}

						.add-code-light {
							&-sign {
								fill: $disabled-text-color;
							}
						}
					}
					&.hide {
						opacity: 0;
					}
					svg {
						width: 32px;
						height: 32px;
					}
				}
				.ember-tooltip {
					padding: 8px;
				}
				.field-labels-selector {
					background-color: $white;
					border-radius: 4px;
					box-shadow: 0 1px 5px 0 $toggleSlider;
					left: 100%;
					margin-left: 16px;
					position: absolute;
					right: 0;
					top: 0;
					z-index: 15;
					width: 376px;
					.ember-modal-header {
						padding: 16px;
						.title {
							font-weight: $text-weight-semi-bold;
							@include font-size($title-normal);
						}
						.ember-modal-close {
							cursor: pointer;
							float: right;
							padding-top: 4px;
							background: 0 0;
							border: 1px solid transparent;
							svg {
								fill: $app-muted-color;
								height: 14px;
								width: 14px;
							}
						}
					}
					.ember-modal-body {
						padding: 0 16px 16px;
						.subtitle {
							@include font-size($text-normal);
						}
						.labels-wrapper {
							margin-top: 8px;
							max-height: 160px;
							overflow: auto;
							.payload-label {
								background: $white;
								width: 100%;
								color: $primary-text-color-1;
								text-align: left;
								border: 1px solid $primary-border;
								border-radius: 2px;
								box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
								cursor: pointer;
								display: block;
								min-height: 32px;
								margin: 0 0 8px;
								padding: 6px 16px;
								word-break: break-word;
								&:hover {
									background: $background-color-2;
								}
							}
						}
					}
				}
			}
			.field-error {
				@include font-size($text-normal);
				margin-top: 8px;
				text-align: left;
				color: $app-red-1;
			}
			.field-desc {
				width: 332px;
				margin-top: 8px;
				@include font-size($text-normal);
				text-align: left;
				color: $secondary-text-color-2;
				b {
					font-weight: $text-weight-bold;
				}
			}
		}
		.right {
			min-width: calc(47% - 16px);
			margin: 8px;
		}
	}
	.card-row-wrapper {

		.wrapper-text {
			padding: 8px;
		}

		&.resource, &.metric_name {
			background-color: $section-background-blue;
			border: 1px dashed $app-border-primary;
			padding: 8px;
			border-radius: $radius;

			.card-row{
				background-color: $white;
			}
		}

		&.resource {
			margin-top: 8px;
			border-bottom: none;
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
		}

		&.metric_name {
			border-top: none;
			border-top-left-radius: 0;
			border-top-right-radius: 0;
			padding-top: 0;

			.card-row {
				margin-top: 0;
			}
		}
	}
}