&.alerts_admin_itom_alert_integration_show.page-layout__page-wrapper {
	@import "edit-integration";
	@import "severity-mapping";
	@import "payload-mapping";
	@import "alert-aggregation";
	@import "../sticky-footer";
	.email-container {
		margin: 16px 32px 0;
		width: 60%;
		.email-desc {
			margin: 8px 16px;
			width: 486px;
		}
		@import "authorization";
	}

	.page-content {
		padding: 0 0 88px 0;
		min-height: calc(100vh - 120px);
		margin: 0;
		border-radius: 0;

		.content-wrapper {
			.edit-container{
				padding: 8px 32px 0px 24px;
			}
			.payload-mapping{
				padding: 0 24px;
			}
			.severity-mapping{
				margin: 0 48px;
			}
		}
	}
}
